import _common from './_common';
import axios from "axios";
import {IP_AND_PORT} from "../../config";

class ReserveNow extends _common {
    requestName = "ReserveNow";
    schema = {
        definitions: {
            CustomDataType: {
                description: "This class can be extended with arbitrary JSON properties to allow adding custom data.",
                type: "object",
                properties: {
                    jsonString: {
                        title: "JSON String",
                        description: "If this field is not changed, the whole customData property will be removed",
                        type: "string",
                        default: "{\"vendorId\": \"\"}",
                        maxLength: 10000
                    }
                }
            },
            ConnectorEnumType: {
                description: "This field specifies the connector type.",
                type: "string",
                enum: ["cCCS1", "cCCS2", "cG105", "cTesla", "cType1", "cType2", "s309-1P-16A", "s309-1P-32A",
                    "s309-3P-16A", "s309-3P-32A", "sBS1361", "sCEE-7-7", "sType2", "sType3", "Other1PhMax16A",
                    "Other1PhOver16A", "Other3Ph", "Pan", "wInductive", "wResonant", "Undetermined", "Unknown"]
            },
            IdTokenEnumType: {
                description: "Enumeration of possible idToken types.",
                type: "string",
                enum: ["Central", "eMAID", "ISO14443", "ISO15693", "KeyCode", "Local", "MacAddress", "NoAuthorization"]
            },
            AdditionalInfoType: {
                description: "Contains a case insensitive identifier to use for the authorization and the type of " +
                    "authorization to support multiple forms of identifiers.",
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    additionalIdToken: {
                        description: "This field specifies the additional IdToken.",
                        type: "string",
                        maxLength: 36
                    },
                    type: {
                        description: "This defines the type of the additionalIdToken. This is a custom type, " +
                            "so the implementation needs to be agreed upon by all involved parties.",
                        type: "string",
                        maxLength: 50
                    }
                },
                required: ["additionalIdToken", "type"]
            },
            IdTokenType: {
                description: "Contains a case insensitive identifier to use for the authorization and the type of " +
                    "authorization to support multiple forms of identifiers.",
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    additionalInfo: {
                        type: "array",
                        items: {
                            $ref: "#/definitions/AdditionalInfoType"
                        },
                        minItems: 1
                    },
                    idToken: {
                        description: "IdToken is case insensitive. Might hold the hidden id of an RFID tag, but can for example also contain a UUID.",
                        type: "string",
                        maxLength: 36
                    },
                    type: {
                        $ref: "#/definitions/IdTokenEnumType"
                    }
                },
                required: ["idToken", "type"]
            }
        },
        type: "object",
        properties: {
            customData: {
                $ref: "#/definitions/CustomDataType"
            },
            id: {
                description: "Id of reservation.",
                type: "integer"
            },
            expiryDateTime: {
                description: "Date and time at which the reservation expires.",
                type: "string",
                format: "date-time"
            },
            connectorType: {
                $ref: "#/definitions/ConnectorEnumType"
            },
            idToken: {
                $ref: "#/definitions/IdTokenType"
            },
            evseId: {
                description: "This contains ID of the evse to be reserved.",
                type: "integer"
            },
            groupIdToken: {
                $ref: "#/definitions/IdTokenType"
            }
        },
        required: ["id", "expiryDateTime", "idToken"]
    }

    validate = (formData, errors) => {
        let json = null;
        if (formData.hasOwnProperty("customData")) {
            if (formData.customData.hasOwnProperty("jsonString")) {
                if (formData.customData.jsonString.length > 0) {
                    try {
                        json = JSON.parse(formData.customData.jsonString);
                        if(!json.hasOwnProperty("vendorId")){
                            errors.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                        }
                    } catch {
                        errors.customData.jsonString.addError("cannot parse this field into JSON string");
                    }
                }
            }
        }
        if (formData.idToken.hasOwnProperty("customData")) {
            if (formData.idToken.customData.hasOwnProperty("jsonString")) {
                if (formData.idToken.customData.jsonString.length > 0) {
                    try {
                        json = JSON.parse(formData.idToken.customData.jsonString);
                        if(!json.hasOwnProperty("vendorId")){
                            errors.idToken.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                        }
                    } catch {
                        errors.idToken.customData.jsonString.addError("cannot parse this field into JSON string");
                    }
                }
            }
        }
        formData.idToken.additionalInfo.forEach((value, index) => {
            if(value.customData.hasOwnProperty("jsonString")) {
                if (value.customData.jsonString.length > 0) {
                    try {
                        json = JSON.parse(value.customData.jsonString);
                        if(!json.hasOwnProperty("vendorId")){
                            errors.idToken.additionalInfo[index].customData.jsonString.addError("JSON string should have \"vendorId\" property");
                        }
                    } catch {
                        errors.idToken.additionalInfo[index].customData.jsonString.addError("cannot parse this field into JSON string");
                    }
                }
            }
        });
        if (formData.groupIdToken.hasOwnProperty("customData")) {
            if (formData.groupIdToken.customData.hasOwnProperty("jsonString")) {
                if (formData.groupIdToken.customData.jsonString.length > 0) {
                    try {
                        json = JSON.parse(formData.groupIdToken.customData.jsonString);
                        if(!json.hasOwnProperty("vendorId")){
                            errors.groupIdToken.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                        }
                    } catch {
                        errors.groupIdToken.customData.jsonString.addError("cannot parse this field into JSON string");
                    }
                }
            }
        }
        formData.groupIdToken.additionalInfo.forEach((value, index) => {
            if(value.customData.hasOwnProperty("jsonString")) {
                if (value.customData.jsonString.length > 0) {
                    try {
                        json = JSON.parse(value.customData.jsonString);
                        if(!json.hasOwnProperty("vendorId")){
                            errors.groupIdToken.additionalInfo[index].customData.jsonString.addError("JSON string should have \"vendorId\" property");
                        }
                    } catch {
                        errors.groupIdToken.additionalInfo[index].customData.jsonString.addError("cannot parse this field into JSON string");
                    }
                }
            }
        });
        return errors;
    }

    onSubmit = async ({formData}) => {
        formData = this.formatCustomData(formData);
        formData.idToken = this.formatCustomData(formData.idToken);
        formData.idToken.additionalInfo.forEach((value, index) => {
            formData.idToken.additionalInfo[index] = this.formatCustomData(value);
        });
        formData.groupIdToken = this.formatCustomData(formData.groupIdToken);
        formData.groupIdToken.additionalInfo.forEach((value, index) => {
            formData.groupIdToken.additionalInfo[index] = this.formatCustomData(value);
        });
        const json = this.formatData(formData);
        window.alert(json);
        await axios.post(`http://${IP_AND_PORT}/api/post-request`,
             json, {headers: {'Content-Type': 'application/json'}});
    }
}

export default ReserveNow;