import _common from './_common';
import axios from "axios";
import {IP_AND_PORT} from "../../config";

class SetNetworkProfile extends _common {
    requestName = "SetNetworkProfile";
    schema = {
        definitions: {
            CustomDataType: {
                description: "This class can be extended with arbitrary JSON properties to allow adding custom data.",
                type: "object",
                properties: {
                    jsonString: {
                        title: "JSON String",
                        description: "If this field is not changed, the whole customData property will be removed",
                        type: "string",
                        default: "{\"vendorId\": \"\"}",
                        maxLength: 10000
                    }
                }
            },
            APNAuthenticationEnumType: {
                description: "APN authentication method.",
                type: "string",
                enum: ["CHAP", "NONE", "PAP", "AUTO"]
            },
            OCPPInterfaceEnumType: {
                description: "Applicable Network Interface.",
                type: "string",
                enum: ["Wired0", "Wired1", "Wired2", "Wired3", "Wireless0", "Wireless1", "Wireless2", "Wireless3"]
            },
            OCPPTransportEnumType: {
                description: "Defines the transport protocol (e.g. SOAP or JSON). Note: SOAP is not supported in OCPP 2.0, but is supported by other versions of OCPP.",
                type: "string",
                enum: ["JSON", "SOAP"]
            },
            OCPPVersionEnumType: {
                description: "Defines the OCPP version used for this communication function.",
                type: "string",
                enum: ["OCPP12", "OCPP15", "OCPP16", "OCPP20"]
            },
            VPNEnumType: {
                description: "Type of VPN",
                type: "string",
                enum: ["IKEv2", "IPSec", "L2TP", "PPTP"]
            },
            APNType: {
                description: "Collection of configuration data needed to make a data-connection over a cellular network." +
                    " NOTE: When asking a GSM modem to dial in, it is possible to specify which mobile operator should " +
                    "be used. This can be done with the mobile country code (MCC) in combination with a mobile network c" +
                    "ode (MNC). Example: If your preferred network is Vodafone Netherlands, the MCC=204 and the MNC=04 " +
                    "which means the key PreferredNetwork = 20404 Some modems allows to specify a preferred network, " +
                    "which means, if this network is not available, a different network is used. If you specify " +
                    "UseOnlyPreferredNetwork and this network is not available, the modem will not dial in.",
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    apn: {
                        description: "The Access Point Name as an URL.",
                        type: "string",
                        maxLength: 512
                    },
                    apnUserName: {
                        description: "APN username.",
                        type: "string",
                        maxLength: 20
                    },
                    apnPassword: {
                        description: "APN Password.",
                        type: "string",
                        maxLength: 20
                    },
                    simPin: {
                        description: "APN. SIM card pin code.",
                        type: "integer"
                    },
                    preferredNetwork: {
                        description: "Preferred network, written as MCC and MNC concatenated.",
                        type: "string",
                        maxLength: 6
                    },
                    useOnlyPreferredNetwork: {
                        description: "Default: false. Use only the preferred Network, do not dial in when not available. See Note.",
                        type: "boolean",
                        default: false
                    },
                    apnAuthentication: {
                        $ref: "#/definitions/APNAuthenticationEnumType"
                    }
                },
                required: ["apn", "apnAuthentication"]
            },
            NetworkConnectionProfileType: {
                description: "The NetworkConnectionProfile defines the functional and technical parameters of a communication link.",
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    apn: {
                        $ref: "#/definitions/APNType"
                    },
                    ocppVersion: {
                        $ref: "#/definitions/OCPPVersionEnumType"
                    },
                    ocppTransport: {
                        $ref: "#/definitions/OCPPTransportEnumType"
                    },
                    ocppCsmsUrl: {
                        description: "URL of the CSMS(s) that this Charging Station communicates with.",
                        type: "string",
                        maxLength: 512
                    },
                    messageTimeout: {
                        description: "Duration in seconds before a message send by the Charging Station via this " +
                            "network connection times-out. The best setting depends on the underlying network " +
                            "and response times of the CSMS. " +
                            "If you are looking for a some guideline: use 30 seconds as a starting point.\r\n",
                        type: "integer"
                    },
                    securityProfile: {
                        description: "This field specifies the security profile used when connecting to the CSMS " +
                            "with this NetworkConnectionProfile.",
                        type: "integer"
                    },
                    ocppInterface: {
                        $ref: "#/definitions/OCPPInterfaceEnumType"
                    },
                    vpn: {
                        $ref: "#/definitions/VPNType"
                    }
                },
                required: ["ocppVersion", "ocppTransport", "ocppCsmsUrl", "messageTimeout", "securityProfile",
                    "ocppInterface"]
            },
            VPNType: {
                description: "VPN Configuration settings",
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    server: {
                        description: "VPN Server Address",
                        type: "string",
                        maxLength: 512
                    },
                    user: {
                        description: "VPN User",
                        type: "string",
                        maxLength: 20
                    },
                    group: {
                        description: "VPN group",
                        type: "string",
                        maxLength: 20
                    },
                    password: {
                        description: "VPN Password.",
                        type: "string",
                        maxLength: 20
                    },
                    key: {
                        description: "VPN shared secret",
                        type: "string",
                        maxLength: 255
                    },
                    type: {
                        $ref: "#/definitions/VPNEnumType"
                    }
                },
                required: ["server", "user", "password", "key", "type"]
            }
        },
        type: "object",
        properties: {
            customData: {
                $ref: "#/definitions/CustomDataType"
            },
            configurationSlot: {
                description: "Slot in which the configuration should be stored.",
                type: "integer"
            },
            connectionData: {
                $ref: "#/definitions/NetworkConnectionProfileType"
            }
        }
    }

    validate = (formData, errors) => {
        let json = null;
        if (formData.customData.jsonString.length > 0) {
            try {
                json = JSON.parse(formData.customData.jsonString);
                if(!json.hasOwnProperty("vendorId")){
                    errors.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                }
            } catch (e) {
                errors.customData.jsonString.addError("cannot parse this field into JSON string");
            }
        }
        if (formData.connectionData.customData.jsonString.length > 0) {
            try {
                json = JSON.parse(formData.connectionData.customData.jsonString);
                if(!json.hasOwnProperty("vendorId")){
                    errors.connectionData.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                }
            } catch (e) {
                errors.connectionData.customData.jsonString.addError("cannot parse this field into JSON string");
            }
        }
        if (formData.connectionData.apn.customData.jsonString.length > 0) {
            try {
                json = JSON.parse(formData.connectionData.apn.customData.jsonString);
                if(!json.hasOwnProperty("vendorId")){
                    errors.connectionData.apn.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                }
            } catch (e) {
                errors.connectionData.apn.customData.jsonString.addError("cannot parse this field into JSON string");
            }
        }
        if (formData.connectionData.vpn.customData.jsonString.length > 0) {
            try {
                json = JSON.parse(formData.connectionData.vpn.customData.jsonString);
                if(!json.hasOwnProperty("vendorId")){
                    errors.connectionData.vpn.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                }
            } catch (e) {
                errors.connectionData.vpn.customData.jsonString.addError("cannot parse this field into JSON string");
            }
        }
        return errors;
    }

    onSubmit = async ({formData}) => {
        formData = this.formatCustomData(formData);
        formData.connectionData = this.formatCustomData(formData.connectionData);
        formData.connectionData.apn = this.formatCustomData(formData.connectionData.apn);
        formData.connectionData.vpn = this.formatCustomData(formData.connectionData.vpn);
        const json = this.formatData(formData);
        window.alert(json);
        await axios.post(`http://${IP_AND_PORT}/api/post-request`,
             json, {headers: {'Content-Type': 'application/json'}});
    }
}

export default SetNetworkProfile;