import _common from "./_common";
import axios from "axios";
import {IP_AND_PORT} from "../../config";

class CustomerInformation extends _common {
    requestName="CustomerInformation";
    schema = {
        definitions: {
            CustomDataType: {
                description: "This class can be extended with arbitrary JSON properties to allow adding custom data.",
                type: "object",
                properties: {
                    jsonString: {
                        title: "JSON String",
                        description: "If this field is not changed, the whole customData property will be removed",
                        type: "string",
                        default: "{\"vendorId\": \"\"}",
                        maxLength: 10000
                    }
                }
            },
            HashAlgorithmEnumType: {
                description: "Used algorithms for the hashes provided.",
                type: "string",
                enum: ["SHA256", "SHA384", "SHA512"]
            },
            IdTokenEnumType: {
                description: "Enumeration of possible idToken types.",
                type: "string",
                enum: ["Central", "eMAID", "ISO14443", "ISO15693", "KeyCode", "Local", "MacAddress", "NoAuthorization"]
            },
            AdditionalInfoType: {
                description: "Contains a case insensitive identifier to use for the authorization and the type of authorization to support multiple forms of identifiers.",
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    additionalIdToken: {
                        description: "This field specifies the additional IdToken.",
                        type: "string",
                        maxLength: 36
                    },
                    type: {
                        description: "This defines the type of the additionalIdToken. This is a custom type, so the implementation needs to be agreed upon by all involved parties.",
                        type: "string",
                        maxLength: 50
                    }
                },
                required: ["additionalIdToken", "type"]
            },
            CertificateHashDataType: {
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    hashAlgorithm: {
                        $ref: "#/definitions/HashAlgorithmEnumType"
                    },
                    issuerNameHash: {
                        description: "Hashed value of the Issuer DN (Distinguished Name).",
                        type: "string",
                        maxLength: 128
                    },
                    issuerKeyHash: {
                        description: "Hashed value of the issuers public key",
                        type: "string",
                        maxLength: 128
                    },
                    serialNumber: {
                        description: "The serial number of the certificate.",
                        type: "string",
                        maxLength: 40
                    }
                },
                required: ["hashAlgorithm", "issuerNameHash", "issuerKeyHash", "serialNumber"]
            },
            IdTokenType: {
                description: "Contains a case insensitive identifier to use for the authorization and the type of authorization to support multiple forms of identifiers.",
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    additionalInfo: {
                        type: "array",
                        items: {
                            $ref: "#/definitions/AdditionalInfoType"
                        },
                        minItems: 1
                    },
                    idToken: {
                        description: "IdToken is case insensitive. Might hold the hidden id of an RFID tag, but can for example also contain a UUID.",
                        type: "string",
                        maxLength: 36
                    },
                    type: {
                        $ref: "#/definitions/IdTokenEnumType"
                    }
                },
                required: [ "idToken", "type" ]
            }
        },
        type: "object",
        properties: {
            customData: {
                $ref: "#/definitions/CustomDataType"
            },
            customerCertificate: {
                $ref: "#/definitions/CertificateHashDataType"
            },
            idToken: {
                $ref: "#/definitions/IdTokenType"
            },
            requestId: {
                description: "The Id of the request.",
                type: "integer"
            },
            report: {
                description: "Flag indicating whether the Charging Station should return NotifyCustomerInformationRequest messages containing information about the customer referred to.",
                type: "boolean"
            },
            clear: {
                description: "Flag indicating whether the Charging Station should clear all information about the customer referred to.",
                type: "boolean"
            },
            customerIdentifier: {
                description: "A (e.g. vendor specific) identifier of the customer this request refers to. This field contains a custom identifier other than IdToken and Certificate.\r\nOne of the possible identifiers (customerIdentifier, customerIdToken or customerCertificate) should be in the request message.",
                type: "string",
                maxLength: 64
            }
        },
        required: ["requestId", "report", "clear"]
    };

    validate = (formData, errors) => {
        let json = null;
        if (formData.hasOwnProperty("customData")) {
            if (formData.customData.hasOwnProperty("jsonString")){
                if(formData.customData.jsonString.length > 0){
                    try {
                        json = JSON.parse(formData.customData.jsonString);
                        if(!json.hasOwnProperty("vendorId")){
                            errors.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                        }
                    } catch (e) {
                        errors.customData.jsonString.addError("cannot parse this field into JSON string");
                    }
                }
            }
        }
        if (formData.customerCertificate.hasOwnProperty("customData")) {
            if (formData.customerCertificate.customData.hasOwnProperty("jsonString")){
                if(formData.customerCertificate.customData.jsonString.length > 0){
                    try {
                        json = JSON.parse(formData.customerCertificate.customData.jsonString);
                        if(!json.hasOwnProperty("vendorId")){
                            errors.customerCertificate.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                        }
                    } catch {
                        errors.customerCertificate.customData.jsonString.addError("cannot parse this field into JSON string");
                    }
                }
            }
        }
        if (formData.idToken.hasOwnProperty("customData")) {
            if (formData.idToken.customData.hasOwnProperty("jsonString")){
                if(formData.idToken.customData.jsonString.length > 0){
                    try {
                        json = JSON.parse(formData.idToken.customData.jsonString);
                        if(!json.hasOwnProperty("vendorId")){
                            errors.idToken.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                        }
                    } catch (e) {
                        errors.idToken.customData.jsonString.addError("cannot parse this field into JSON string");
                    }
                }
            }
        }
        if(formData.idToken.hasOwnProperty("additionalInfo")){
            formData.idToken.additionalInfo.forEach((value, index) => {
                try {
                    json = JSON.parse(value.customData.jsonString);
                    if(!json.hasOwnProperty("vendorId")){
                        errors.idToken.additionalInfo[index].customData.jsonString.addError("JSON string should have \"vendorId\" property");
                    }
                } catch (e) {
                    errors.idToken.additionalInfo[index].customData.jsonString.addError("cannot parse this field into JSON string");
                }
            });
        }
        return errors;
    }

    onSubmit = async ({formData}) => {
        formData = this.formatCustomData(formData);
        formData.customerCertificate = this.formatCustomData(formData.customerCertificate);
        formData.idToken = this.formatCustomData(formData.idToken);
        if (formData.idToken.hasOwnProperty("additionalInfo")){
            formData.idToken.additionalInfo.forEach((value, index) => {
                formData.idToken.additionalInfo[index] = this.formatCustomData(value)
            })
        }
        const json = this.formatData(formData);
        window.alert(json);
        await axios.post(`http://${IP_AND_PORT}/api/post-request`,
             json, {headers: {'Content-Type': 'application/json'}});
    }

}

export default CustomerInformation;