import _common from './_common';
import axios from "axios";
import {IP_AND_PORT} from "../../config";

class SetDisplayMessage extends _common {
    requestName = "SetDisplayMessage";
    schema = {
        definitions: {
            CustomDataType: {
                description: "This class can be extended with arbitrary JSON properties to allow adding custom data.",
                type: "object",
                properties: {
                    jsonString: {
                        title: "JSON String",
                        description: "If this field is not changed, the whole customData property will be removed",
                        type: "string",
                        default: "{\"vendorId\": \"\"}",
                        maxLength: 10000
                    }
                }
            },
            MessageFormatEnumType: {
                description: "Format of the message.",
                type: "string",
                enum: ["ASCII", "HTML", "URI", "UTF8"]
            },
            MessagePriorityEnumType: {
                description: "With what priority should this message be shown",
                type: "string",
                enum: ["AlwaysFront", "InFront", "NormalCycle"]
            },
            MessageStateEnumType: {
                description: "During what state should this message be shown. When omitted this message should be " +
                    "shown in any state of the Charging Station.",
                type: "string",
                enum: ["Charging", "Faulted", "Idle", "Unavailable"]
            },
            ComponentType: {
                description: "A physical or logical component",
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    evse: {
                        $ref: "#/definitions/EVSEType"
                    },
                    name: {
                        description: "Name of the component. Name should be taken from the list of standardized " +
                            "component names whenever possible. Case Insensitive. strongly advised to use Camel Case.",
                        type: "string",
                        maxLength: 50
                    },
                    instance: {
                        description: "Name of instance in case the component exists as multiple instances. Case " +
                            "Insensitive. strongly advised to use Camel Case.\r\n",
                        type: "string",
                        maxLength: 50
                    }
                },
                required: ["name"]
            },
            EVSEType: {
                description: "Electric Vehicle Supply Equipment",
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    id: {
                        description: "EVSE Identifier. This contains a number > 0 designating an EVSE of the " +
                            "Charging Station.",
                        type: "integer"
                    },
                    connectorId: {
                        description: "An id to designate a specific connector (on an EVSE) by connector index number.",
                        type: "integer"
                    }
                },
                required: ["id"]
            },
            MessageContentType: {
                description: "Contains message details, for a message to be displayed on a Charging Station.",
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    format: {
                        $ref: "#/definitions/MessageFormatEnumType"
                    },
                    language: {
                        description: "Message language identifier. Contains a language code as defined in " +
                            "<<ref-RFC5646,[RFC5646]>>.",
                        type: "string",
                        maxLength: 8
                    },
                    content: {
                        description: "Message contents.",
                        type: "string",
                        maxLength: 512
                    }
                },
                required: ["format", "content"]
            },
            MessageInfoType: {
                description: "Contains message details, for a message to be displayed on a Charging Station.",
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    display: {
                        $ref: "#/definitions/ComponentType"
                    },
                    id: {
                        description: "Master resource identifier, unique within an exchange context. It is defined " +
                            "within the OCPP context as a positive Integer value (greater or equal to zero).",
                        type: "integer"
                    },
                    priority: {
                        $ref: "#/definitions/MessagePriorityEnumType"
                    },
                    state: {
                        $ref: "#/definitions/MessageStateEnumType"
                    },
                    startDateTime: {
                        description: "From what date-time should this message be shown. If omitted: directly.",
                        type: "string",
                        format: "date-time"
                    },
                    endDateTime: {
                        description: "Until what date-time should this message be shown, after this date/time this " +
                            "message SHALL be removed.",
                        type: "string",
                        format: "date-time"
                    },
                    transactionId: {
                        description: "During which transaction shall this message be shown.\r\nMessage SHALL be " +
                            "removed by the Charging Station after transaction has\r\nended.",
                        type: "string",
                        maxLength: 36
                    },
                    message: {
                        $ref: "#/definitions/MessageContentType"
                    }
                },
                required: ["id", "priority", "message"]
            }
        },
        type: "object",
        properties: {
            customData: {
                $ref: "#/definitions/CustomDataType"
            },
            message: {
                $ref: "#/definitions/MessageInfoType"
            }
        },
        required: ["message"]
    }

    validate = (formData, errors) => {
        let json = null;
        if (formData.customData.jsonString.length > 0) {
            try {
                json = JSON.parse(formData.customData.jsonString);
                if(!json.hasOwnProperty("vendorId")){
                    errors.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                }
            } catch {
                errors.customData.jsonString.addError("cannot parse this field into JSON string");
            }
        }
        if (formData.message.customData.jsonString.length > 0) {
            try {
                json = JSON.parse(formData.message.customData.jsonString);
                if(!json.hasOwnProperty("vendorId")){
                    errors.message.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                }
            } catch {
                errors.message.customData.jsonString.addError("cannot parse this field into JSON string");
            }
        }
        if (formData.message.message.customData.jsonString.length > 0) {
            try {
                json = JSON.parse(formData.message.message.customData.jsonString);
                if(!json.hasOwnProperty("vendorId")){
                    errors.message.message.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                }
            } catch {
                errors.message.message.customData.jsonString.addError("cannot parse this field into JSON string");
            }
        }
        if (formData.message.display.customData.jsonString.length > 0) {
            try {
                json = JSON.parse(formData.message.display.customData.jsonString);
                if(!json.hasOwnProperty("vendorId")){
                    errors.message.display.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                }
            } catch {
                errors.message.display.customData.jsonString.addError("cannot parse this field into JSON string");
            }
        }
        if (formData.message.display.evse.customData.jsonString.length > 0) {
            try {
                json = JSON.parse(formData.message.display.evse.customData.jsonString);
                if(!json.hasOwnProperty("vendorId")){
                    errors.message.display.evse.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                }
            } catch {
                errors.message.display.evse.customData.jsonString.addError("cannot parse this field into JSON string");
            }
        }
        return errors;
    }

    onSubmit = async ({formData}) => {
        formData = this.formatCustomData(formData);
        formData.message = this.formatCustomData(formData.message);
        formData.message.message = this.formatCustomData(formData.message.message);
        formData.message.display = this.formatCustomData(formData.message.display);
        formData.message.display.evse = this.formatCustomData(formData.message.display.evse);
        const json = this.formatData(formData);
        window.alert(json);
        await axios.post(`http://${IP_AND_PORT}/api/post-request`,
             json, {headers: {'Content-Type': 'application/json'}});
    }
}

export default SetDisplayMessage;