import Common from './_common';

class SignCertificate extends Common {
    requestName = "SignCertificate";
    schema = {
        definitions: {
            CustomDataType: {
                description: "This class can be extended with arbitrary JSON properties to allow adding custom data.",
                type: "object",
                properties: {
                    jsonString: {
                        title: "JSON String",
                        description: "If this field is not changed, the whole customData property will be removed",
                        type: "string",
                        default: "{\"vendorId\": \"\"}",
                        maxLength: 10000
                    }
                }
            },
            CertificateSigningUseEnumType: {
                description: "Indicates the type of certificate that is to be signed. When omitted the certificate " +
                    "is to be used for both the 15118 connection (if implemented) and the Charging Station to CSMS connection.",
                type: "string",
                enum: ["ChargingStationCertificate", "V2GCertificate"]
            }
        },
        type: "object",
        properties: {
            customData: {
                $ref: "#/definitions/CustomDataType"
            },
            csr: {
                description: "The Charging Station SHALL send the public key in form of a Certificate Signing Request " +
                    "(CSR) as described in RFC 2986 [22] and then PEM encoded, using the <<SignCertificateRequest>> message.",
                type: "string",
                maxLength: 5500
            },
            certificateType: {
                $ref: "#/definitions/CertificateSigningUseEnumType"
            }
        },
        required: ["csr"]
    }
}

export default SignCertificate;