import _common from './_common';
import axios from "axios";
import {IP_AND_PORT} from "../../config";

class SetVariableMonitoring extends _common {
     requestName="SetVariableMonitoring";
     schema={
        definitions: {
            CustomDataType: {
                description: "This class can be extended with arbitrary JSON properties to allow adding custom data.",
                type: "object",
                properties: {
                    jsonString: {
                        title: "JSON String",
                        description: "If this field is not changed, the whole customData property will be removed",
                        type: "string",
                        default: "{\"vendorId\": \"\"}",
                        maxLength: 10000
                    }
                }
            },
            MonitorEnumType: {
                description: "The type of this monitor, e.g. a threshold, delta or periodic monitor.",
                type: "string",
                enum: ["UpperThreshold", "LowerThreshold", "Delta", "Periodic", "PeriodicClockAligned"]
            },
            ComponentType: {
                description: "A physical or logical component",
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    evse: {
                        $ref: "#/definitions/EVSEType"
                    },
                    name: {
                        description: "Name of the component. Name should be taken from the list of standardized " +
                            "component names whenever possible. Case Insensitive. strongly advised to use Camel Case.",
                        type: "string",
                        maxLength: 50
                    },
                    instance: {
                        description: "Name of instance in case the component exists as multiple instances. Case " +
                            "Insensitive. strongly advised to use Camel Case.",
                        type: "string",
                        maxLength: 50
                    }
                },
                required: ["name"]
            },
            EVSEType: {
                description: "Electric Vehicle Supply Equipment",
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    id: {
                        description: "EVSE Identifier. This contains a number > 0 designating an EVSE of the Charging Station.",
                        type: "integer"
                    },
                    connectorId: {
                        description: "An id to designate a specific connector (on an EVSE) by connector index number.",
                        type: "integer"
                    }
                },
                required: ["id"]
            },
            SetMonitoringDataType: {
                description: "Class to hold parameters of SetVariableMonitoring request.",
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    id: {
                        description: "An id SHALL only be given to replace an existing monitor. The Charging Station " +
                            "handles the generation of id's for new monitors.",
                        type: "integer"
                    },
                    transaction: {
                        description: "Monitor only active when a transaction is ongoing on a component relevant " +
                            "to this transaction. Default = false.",
                        type: "boolean",
                        default: false
                    },
                    value: {
                        description: "Value for threshold or delta monitoring. For Periodic or PeriodicClockAligned " +
                            "this is the interval in seconds.",
                        type: "number"
                    },
                    type: {
                        $ref: "#/definitions/MonitorEnumType"
                    },
                    severity: {
                        description: "The severity that will be assigned to an event that is triggered by this monitor. " +
                            "The severity range is 0-9, with 0 as the highest and 9 as the lowest severity level. " +
                            "The severity levels have the following meaning: +*0-Danger* + Indicates lives are " +
                            "potentially in danger. Urgent attention is needed and action should be taken immediately. " +
                            "+*1-Hardware Failure* + Indicates that the Charging Station is unable to continue " +
                            "regular operations due to Hardware issues. Action is required. " +
                            "+ *2-System Failure* + Indicates that the Charging Station is unable to continue " +
                            "regular operations due to software or minor hardware issues. Action is required. " +
                            "+*3-Critical* + Indicates a critical error. Action is required. " +
                            "+*4-Error* + Indicates a non-urgent error. Action is required. " +
                            "+*5-Alert* + Indicates an alert event. Default severity for any type of monitoring event.  " +
                            "+*6-Warning* + Indicates a warning event. Action may be required. " +
                            "+ *7-Notice* + Indicates an unusual event. No immediate action is required. " +
                            "+ *8-Informational* + Indicates a regular operational event. May be used for reporting, " +
                            "measuring throughput, etc. No action is required. " +
                            "+ *9-Debug* + Indicates information useful to developers for debugging, not useful during operations.",
                        type: "integer"
                    },
                    component: {
                        $ref: "#/definitions/ComponentType"
                    },
                    variable: {
                        "$ref": "#/definitions/VariableType"
                    }
                },
                required: ["value", "type", "severity", "component", "variable"]
            },
            VariableType: {
                description: "Reference key to a component-variable.",
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    name: {
                        description: "Name of the variable. Name should be taken from the list of standardized " +
                            "variable names whenever possible. Case Insensitive. strongly advised to use Camel Case.",
                        type: "string",
                        maxLength: 50
                    },
                    instance: {
                        description: "Name of instance in case the variable exists as multiple instances. " +
                            "Case Insensitive. strongly advised to use Camel Case.",
                        type: "string",
                        maxLength: 50
                    }
                },
                required: ["name"]
            }
        },
        type: "object",
        properties: {
            customData: {
                $ref: "#/definitions/CustomDataType"
            },
            setMonitoringData: {
                type: "array",
                items: {
                    $ref: "#/definitions/SetMonitoringDataType"
                },
                minItems: 1
            }
        },
         required: ["setMonitoringData"]
     }

     validate = (formData, errors) => {
         let json = null;
         if(formData.customData.jsonString.length > 0){
             try {
                 json = JSON.parse(formData.customData.jsonString);
                 if(!json.hasOwnProperty("vendorId")){
                     errors.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                 }
             } catch {
                 errors.customData.jsonString.addError("cannot parse this field into JSON string");
             }
         }
        formData.setMonitoringData.forEach((value, index) => {
            if(value.customData.jsonString.length > 0) {
                try {
                    json = JSON.parse(value.customData.jsonString);
                    if(!json.hasOwnProperty("vendorId")){
                        errors.setMonitoringData[index].customData.jsonString.addError("JSON string should have \"vendorId\" property");
                    }
                } catch {
                    errors.setMonitoringData[index].customData.jsonString.addError("cannot parse this field into JSON string");
                }
            }
            if(value.component.customData.jsonString.length > 0){
                try {
                    json = JSON.parse(value.component.customData.jsonString);
                    if(!json.hasOwnProperty("vendorId")){
                        errors.setMonitoringData[index].component.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                    }
                } catch {
                    errors.setMonitoringData[index].component.customData.jsonString.addError("cannot parse this field into JSON string");
                }
            }
            if(value.component.evse.customData.jsonString.length > 0){
                try {
                    json = JSON.parse(value.component.evse.customData.jsonString);
                    if(!json.hasOwnProperty("vendorId")){
                        errors.setMonitoringData[index].component.evse.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                    }
                } catch {
                    errors.setMonitoringData[index].component.evse.customData.jsonString.addError("cannot parse this field into JSON string");
                }
            }
            if(value.component.variable.customData.jsonString.length > 0){
                try {
                    json = JSON.parse(value.component.variable.customData.jsonString);
                    if(!json.hasOwnProperty("vendorId")){
                        errors.setMonitoringData[index].component.variable.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                    }
                } catch {
                    errors.setMonitoringData[index].component.variable.customData.jsonString.addError("cannot parse this field into JSON string");
                }
            }
        });
        return errors;
    }

    onSubmit = async ({formData}) => {
        formData = this.formatCustomData(formData);
        formData.setMonitoringData.forEach((value, index) => {
            formData.setMonitoringData[index] = this.formatCustomData(value);
            formData.setMonitoringData[index].component = this.formatCustomData(value.component);
            formData.setMonitoringData[index].component.evse = this.formatCustomData(value.component.evse);
            formData.setMonitoringData[index].component.variable = this.formatCustomData(value.component.variable);
        });
        const json = this.formatData(formData);
        window.alert(json);
        await axios.post(`http://${IP_AND_PORT}/api/post-request`,
             json, {headers: {'Content-Type': 'application/json'}});
    }
}

export default SetVariableMonitoring;