import _common from "./_common";
import axios from "axios";
import {IP_AND_PORT} from "../../config";

export class ClearChargingProfile extends _common {
    requestName = "ClearChargingProfile";
    schema = {
        definitions: {
            CustomDataType: {
                description: "This class does not get 'AdditionalProperties = false' in the schema generation, so it can be extended with arbitrary JSON properties to allow adding custom data.",
                type: "object",
                properties: {
                    jsonString: {
                        title: "JSON String",
                        description: "If this field is not changed, the whole customData property will be removed",
                        type: "string",
                        default: "{\"vendorId\": \"\"}",
                        maxLength: 10000
                    }
                }
            },
            ChargingProfilePurposeEnumType: {
                description: "Specifies to purpose of the charging profiles that will be cleared, if they meet the other criteria in the request.",
                type: "string",
                enum: [
                    "ChargingStationExternalConstraints",
                    "ChargingStationMaxProfile",
                    "TxDefaultProfile",
                    "TxProfile"
                ]
            },
            ClearChargingProfileType: {
                description: "ChargingProfile consists of a ChargingSchedule, describing the amount of power or current that can be delivered per time interval.",
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    evseId: {
                        description: "Specifies the id of the EVSE for which to clear charging profiles. An evseId of zero (0) specifies the charging profile for the overall Charging Station. Absence of this parameter means the clearing applies to all charging profiles that match the other criteria in the request.",
                        type: "integer"
                    },
                    chargingProfilePurpose: {
                        $ref: "#/definitions/ChargingProfilePurposeEnumType"
                    },
                    stackLevel: {
                        description: "Specifies the stackLevel for which charging profiles will be cleared, if they meet the other criteria in the request.",
                        type: "integer"
                    }
                }
            }
        },
        type: "object",
        properties: {
            customData: {
                $ref: "#/definitions/CustomDataType"
            },
            chargingProfileId: {
                description: "The Id of the charging profile to clear.",
                type: "integer"
            },
            chargingProfileCriteria: {
                $ref: "#/definitions/ClearChargingProfileType"
            }
        }
    }

    validate = (formData, errors) => {
        let json = null;
        if(formData.customData.jsonString.length > 0){
            try {
                json = JSON.parse(formData.customData.jsonString);
                if(!json.hasOwnProperty("vendorId")){
                    errors.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                }
            } catch (e) {
                errors.customData.jsonString.addError("cannot parse this field into JSON string");
                return errors;
            }
        }
        if(formData.chargingProfileCriteria.customData.jsonString.length > 0){
            try {
                JSON.parse(formData.chargingProfileCriteria.customData.jsonString);
            } catch (e) {
                errors.chargingProfileCriteria.customData.jsonString.addError("cannot parse this field into JSON string");
                return errors;
            }
        }
        return errors;
    }

    onSubmit = async ({formData}) => {
        formData = this.formatCustomData(formData);
        formData.chargingProfileCriteria = this.formatCustomData(formData.chargingProfileCriteria);
        const json = this.formatData(formData);
        window.alert(json);
        await axios.post(`http://${IP_AND_PORT}/api/post-request`,
             json, {headers: {'Content-Type': 'application/json'}});
    }

}

export default ClearChargingProfile;