import Common from './_common';
import axios from "axios";
import {IP_AND_PORT} from "../../config";

class UpdateFirmware extends Common {
    requestName = "UpdateFirmware";
    schema = {
        definitions: {
            CustomDataType: {
                description: "This class can be extended with arbitrary JSON properties to allow adding custom data.",
                type: "object",
                properties: {
                    jsonString: {
                        title: "JSON String",
                        description: "If this field is not changed, the whole customData property will be removed",
                        type: "string",
                        default: "{\"vendorId\": \"\"}",
                        maxLength: 10000
                    }
                }
            },
            FirmwareType: {
                description: "Represents a copy of the firmware that can be loaded/updated on the Charging Station.",
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    location: {
                        description: "URI defining the origin of the firmware.",
                        type: "string",
                        maxLength: 512
                    },
                    retrieveDateTime: {
                        description: "Date and time at which the firmware shall be retrieved.",
                        type: "string",
                        format: "date-time"
                    },
                    installDateTime: {
                        description: "Date and time at which the firmware shall be installed.",
                        type: "string",
                        format: "date-time"
                    },
                    signingCertificate: {
                        description: "Certificate with which the firmware was signed. PEM encoded X.509 certificate.",
                        type: "string",
                        maxLength: 5500
                    },
                    signature: {
                        description: "Base64 encoded firmware signature.",
                        type: "string",
                        maxLength: 800
                    }
                },
                required: ["location", "retrieveDateTime"]
            }
        },
        type: "object",
        properties: {
            customData: {
                $ref: "#/definitions/CustomDataType"
            },
            retries: {
                description: "This specifies how many times Charging Station must try to download the firmware " +
                    "before giving up. If this field is not present, it is left to Charging Station to decide how " +
                    "many times it wants to retry.",
                type: "integer"
            },
            retryInterval: {
                description: "The interval in seconds after which a retry may be attempted. If this field is not " +
                    "present, it is left to Charging Station to decide how long to wait between attempts.",
                type: "integer"
            },
            requestId: {
                description: "The Id of this request",
                type: "integer"
            },
            firmware: {
                $ref: "#/definitions/FirmwareType"
            }
        },
        required: ["requestId", "firmware"]
    }

    validate = (formData, errors) => {
        let json = null;
        if (formData.customData.jsonString.length > 0) {
            try {
                json = JSON.parse(formData.customData.jsonString);
                if(!json.hasOwnProperty("vendorId")){
                    errors.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                }
            } catch {
                errors.customData.jsonString.addError("cannot parse this field into JSON string");
            }
        }
        if (formData.firmware.customData.jsonString.length > 0) {
            try {
                json = JSON.parse(formData.firmware.customData.jsonString);
                if(!json.hasOwnProperty("vendorId")){
                    errors.firmware.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                }
            } catch {
                errors.firmware.customData.jsonString.addError("cannot parse this field into JSON string");
            }
        }
        return errors;
    }

    onSubmit = async ({formData}) => {
        formData = this.formatCustomData(formData);
        formData.firmware = this.formatCustomData(formData.firmware);
        const json = this.formatData(formData);
        window.alert(json);
        await axios.post(`http://${IP_AND_PORT}/api/post-request`,
             json, {headers: {'Content-Type': 'application/json'}});
    }
}

export default UpdateFirmware;