import _common from './_common';
import axios from "axios";
import {IP_AND_PORT} from "../../config";

class GetLog extends _common {
     requestName="GetLog";
     schema={
        definitions: {
            CustomDataType: {
                description: "This class can be extended with arbitrary JSON properties to allow adding custom data.",
                type: "object",
                properties: {
                    jsonString: {
                        title: "JSON String",
                        description: "If this field is not changed, the whole customData property will be removed",
                        type: "string",
                        default: "{\"vendorId\": \"\"}",
                        maxLength: 10000
                    }
                }
            },
            LogEnumType: {
                description: "This contains the type of log file that the Charging Station should send.",
                type: "string",
                enum: ["DiagnosticsLog", "SecurityLog"]
            },
            LogParametersType: {
                description: "Generic class for the configuration of logging entries.",
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    remoteLocation: {
                        description: "The URL of the location at the remote system where the log should be stored.",
                        type: "string",
                        maxLength: 512
                    },
                    oldestTimestamp: {
                        description: "This contains the date and time of the oldest logging information to include " +
                            "in the diagnostics.",
                        type: "string",
                        format: "date-time"
                    },
                    latestTimestamp: {
                        description: "This contains the date and time of the latest logging information to include " +
                            "in the diagnostics.",
                        type: "string",
                        format: "date-time"
                    }
                },
                required: ["remoteLocation"]
            }
        },
        type: "object",
        properties: {
            customData: {
                $ref: "#/definitions/CustomDataType"
            },
            log: {
                $ref: "#/definitions/LogParametersType"
            },
            logType: {
                $ref: "#/definitions/LogEnumType"
            },
            requestId: {
                description: "The Id of this request",
                type: "integer"
            },
            retries: {
                description: "This specifies how many times the Charging Station must try to upload the log before " +
                    "giving up. If this field is not present, it is left to Charging Station to decide how many " +
                    "times it wants to retry.",
                type: "integer"
            },
            retryInterval: {
                description: "The interval in seconds after which a retry may be attempted. If this field is not " +
                    "present, it is left to Charging Station to decide how long to wait between attempts.",
                type: "integer"
            }
        },
         required: ["logType", "requestId", "log"]
     }

    validate = (formData, errors) => {
         let json = null;
        if (formData.hasOwnProperty("customData")) {
            if (formData.customData.hasOwnProperty("jsonString")){
                if(formData.customData.jsonString.length > 0){
                    try {
                        json = JSON.parse(formData.customData.jsonString);
                        if(!json.hasOwnProperty("vendorId")){
                            errors.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                        }
                    } catch {
                        errors.customData.jsonString.addError("cannot parse this field into JSON string");
                    }
                }

            }
        }
        if (formData.log.hasOwnProperty("customData")) {
            if (formData.log.customData.hasOwnProperty("jsonString")){
                if(formData.log.customData.jsonString.length > 0){
                    try {
                        json = JSON.parse(formData.log.customData.jsonString);
                        if(!json.hasOwnProperty("vendorId")){
                            errors.log.customData.jsonString.addError("JSON string should have \"vendorId\" property")
                        }
                    } catch (e) {
                        errors.log.customData.jsonString.addError("cannot parse this field into JSON string");
                    }
                }
            }
        }
        return errors;
    }

    onSubmit = async ({formData}) => {
        formData = this.formatCustomData(formData);
        formData.log = this.formatCustomData(formData.log);
        const json = this.formatData(formData);
        window.alert(json);
        await axios.post(`http://${IP_AND_PORT}/api/post-request`,
             json, {headers: {'Content-Type': 'application/json'}});
    }
}

export default GetLog;