import _common from "./_common";
import axios from "axios";
import {IP_AND_PORT} from "../../config";

class DataTransfer extends _common {
    requestName="DataTransfer";
    schema = {
        definitions: {
            CustomDataType: {
                description: "This class can be extended with arbitrary JSON properties to allow adding custom data.",
                type: "object",
                properties: {
                    jsonString: {
                        title: "JSON String",
                        description: "If this field is not changed, the whole customData property will be removed",
                        type: "string",
                        default: "{\"vendorId\": \"\"}",
                        maxLength: 10000
                    }
                }
            }
        },
        type: "object",
        properties: {
            customData: {
                $ref: "#/definitions/CustomDataType"
            },
            messageId: {
                description: "May be used to indicate a specific message or implementation.",
                type: "string",
                maxLength: 50
            },
            data: {
                description: "Data without specified length or format. This needs to be decided by both parties (Open to implementation).",
                properties: {
                    jsonString: {
                        title: "JSON string",
                        description: "type any JSON string you need",
                        default: "{}",
                        type: "string"
                    }
                }
            },
            vendorId: {
                description: "This identifies the Vendor specific implementation",
                type: "string",
                maxLength: 255
            }
        },
        required: ["vendorId"]
    };

    validate = (formData, errors) => {
        let json = null;
        if (formData.hasOwnProperty("customData")) {
            if (formData.customData.hasOwnProperty("jsonString")){
                if(formData.customData.jsonString.length > 0){
                    try {
                        json = JSON.parse(formData.customData.jsonString);
                        if(!json.hasOwnProperty("vendorId")){
                            errors.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                        }
                    } catch {
                        errors.customData.jsonString.addError("cannot parse this field into JSON string");
                    }
                }
            }
        }
        if (formData.hasOwnProperty("data")) {
            if (formData.data.hasOwnProperty("jsonString")){
                if(formData.data.jsonString.length > 0){
                    try {
                        json = JSON.parse(formData.data.jsonString);
                    } catch (e) {
                        errors.data.jsonString.addError("cannot parse this field into JSON string");
                    }
                }
            }
        }
        return errors;
    }

    formatData = (formData) => {
        let newFormData = {clientId: this.props.uid, charger: this.props.charger, selected: this.requestName,
            payload: formData};
        newFormData.payload.data = JSON.parse(newFormData.payload.data.jsonString);
        return JSON.stringify(newFormData);
    };

    onSubmit = async ({formData}) => {
        formData = this.formatCustomData(formData);
        const json = this.formatData(formData);
        window.alert(json);
        await axios.post(`http://${IP_AND_PORT}/api/post-request`,
             json, {headers: {'Content-Type': 'application/json'}});
    }
}

export default DataTransfer;