import _common from './_common';
import axios from "axios";
import {IP_AND_PORT} from "../../config";

class SetVariables extends _common {
    requestName = "SetVariables";
    schema = {
        definitions: {
            CustomDataType: {
                description: "This class can be extended with arbitrary JSON properties to allow adding custom data.",
                type: "object",
                properties: {
                    jsonString: {
                        title: "JSON String",
                        description: "If this field is not changed, the whole customData property will be removed",
                        type: "string",
                        default: "{\"vendorId\": \"\"}",
                        maxLength: 10000
                    }
                }
            },
            AttributeEnumType: {
                description: "Type of attribute: Actual, Target, MinSet, MaxSet. Default is Actual when omitted.",
                type: "string",
                default: "Actual",
                enum: ["Actual", "Target", "MinSet", "MaxSet"]
            },
            ComponentType: {
                description: "A physical or logical component",
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    evse: {
                        $ref: "#/definitions/EVSEType"
                    },
                    name: {
                        description: "Name of the component. Name should be taken from the list of standardized " +
                            "component names whenever possible. Case Insensitive. strongly advised to use Camel Case.",
                        type: "string",
                        maxLength: 50
                    },
                    instance: {
                        description: "Name of instance in case the component exists as multiple instances. " +
                            "Case Insensitive. strongly advised to use Camel Case.",
                        type: "string",
                        maxLength: 50
                    }
                },
                required: ["name"]
            },
            EVSEType: {
                description: "Electric Vehicle Supply Equipment",
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    id: {
                        description: "EVSE Identifier. This contains a number > 0 designating an EVSE of the Charging Station.",
                        type: "integer"
                    },
                    connectorId: {
                        description: "An id to designate a specific connector (on an EVSE) by connector index number.",
                        type: "integer"
                    }
                },
                required: ["id"]
            },
            SetVariableDataType: {
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    attributeType: {
                        $ref: "#/definitions/AttributeEnumType"
                    },
                    attributeValue: {
                        description: "Value to be assigned to attribute of variable. The Configuration Variable " +
                            "<<configkey-configuration-value-size,ConfigurationValueSize>> can be used to limit " +
                            "SetVariableData.attributeValue and VariableCharacteristics.valueList. The max size of " +
                            "these values will always remain equal. \r\n",
                        type: "string",
                        maxLength: 1000
                    },
                    component: {
                        $ref: "#/definitions/ComponentType"
                    },
                    variable: {
                        $ref: "#/definitions/VariableType"
                    }
                },
                required: ["attributeValue", "component", "variable"]
            },
            VariableType: {
                description: "Reference key to a component-variable.",
                javaType: "Variable",
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    name: {
                        description: "Name of the variable. Name should be taken from the list of standardized " +
                            "variable names whenever possible. Case Insensitive. strongly advised to use Camel Case.",
                        type: "string",
                        maxLength: 50
                    },
                    instance: {
                        description: "Name of instance in case the variable exists as multiple instances. " +
                            "Case Insensitive. strongly advised to use Camel Case.",
                        type: "string",
                        maxLength: 50
                    }
                },
                required: ["name"]
            }
        },
        type: "object",
        properties: {
            customData: {
                $ref: "#/definitions/CustomDataType"
            },
            setVariableData: {
                type: "array",
                items: {
                    $ref: "#/definitions/SetVariableDataType"
                },
                minItems: 1
            }
        },
        required: ["setVariableData"]
    }

    validate = (formData, errors) => {
        let json = null;
        if (formData.customData.jsonString.length > 0) {
            try {
                json = JSON.parse(formData.customData.jsonString);
                if(!json.hasOwnProperty("vendorId")){
                    errors.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                }
            } catch {
                errors.customData.jsonString.addError("cannot parse this field into JSON string");
            }
        }
        formData.setVariableData.forEach((value, index) => {
            if (value.customData.jsonString.length > 0) {
                try {
                    json = JSON.parse(value.customData.jsonString);
                    if(!json.hasOwnProperty("vendorId")){
                        errors.setVariableData[index].customData.jsonString.addError("JSON string should have \"vendorId\" property");
                    }
                } catch {
                    errors.setVariableData[index].customData.jsonString.addError("cannot parse this field into JSON string");
                }
            }
            if (value.component.customData.jsonString.length > 0){
                try {
                    json = JSON.parse(value.component.customData.jsonString);
                    if(!json.hasOwnProperty("vendorId")){
                        errors.setVariableData[index].component.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                    }
                } catch {
                    errors.setVariableData[index].component.customData.jsonString.addError("cannot parse this field into JSON string");
                }
            }
            if (value.component.evse.customData.jsonString.length > 0){
                try {
                    json = JSON.parse(value.component.evse.customData.jsonString);
                    if(!json.hasOwnProperty("vendorId")){
                        errors.setVariableData[index].component.evse.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                    }
                } catch {
                    errors.setVariableData[index].component.evse.customData.jsonString.addError("cannot parse this field into JSON string");
                }
            }
            if (value.component.variable.customData.jsonString.length > 0){
                try {
                    json = JSON.parse(value.component.variable.customData.jsonString);
                    if(!json.hasOwnProperty("vendorId")){
                        errors.setVariableData[index].component.variable.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                    }
                } catch {
                    errors.setVariableData[index].component.variable.customData.jsonString.addError("cannot parse this field into JSON string");
                }
            }
        });
        return errors;
    }

    onSubmit = async ({formData}) => {
        formData = this.formatCustomData(formData);
        formData.setVariableData.forEach((value, index) => {
            formData.setVariableData[index] = this.formatCustomData(value);
            formData.setVariableData[index].component = this.formatCustomData(value.component);
            formData.setVariableData[index].component.evse = this.formatCustomData(value.component.evse);
            formData.setVariableData[index].component.variable = this.formatCustomData(value.component.variable);
        });
        const json = this.formatData(formData);
        window.alert(json);
        await axios.post(`http://${IP_AND_PORT}/api/post-request`,
             json, {headers: {'Content-Type': 'application/json'}});
    }
}

export default SetVariables;