import React, {Component} from 'react';
import PropTypes from "prop-types";
import axios from 'axios';
import {Accordion, Alert, Form} from "react-bootstrap";
import {IP_AND_PORT} from "../config";
import CancelReservation from "./Forms/cancelReservation"
import CertificateSigned from "./Forms/certificateSigned"
import ChangeAvailability from "./Forms/changeAvailability"
import ClearCache from "./Forms/clearCache";
import ClearChargingProfile from "./Forms/clearChargingProfile";
import ClearDisplayMessage from "./Forms/clearDisplayMessage";
import ClearVariableMonitoring from "./Forms/clearVariableMonioring";
import CostUpdated from "./Forms/costUpdated";
import CustomerInformation from "./Forms/customerInformation";
import DataTransfer from "./Forms/dataTransfer";
import DeleteCertificate from "./Forms/deleteCertificate";
import GetBaseReport from "./Forms/getBaseReport";
import GetChargingProfiles from "./Forms/getChargingProfiles";
import GetCompositeSchedule from "./Forms/getCompositeSchedule";
import GetDisplayMessages from "./Forms/getDisplayMessages";
import GetInstalledCertificateIds from "./Forms/getInstalledCertificateIds";
import GetLocalListVersion from "./Forms/getLocalListVersion";
import GetLog from "./Forms/getLog";
import GetMonitoringReport from "./Forms/getMonitoringReport";
import GetReport from "./Forms/getReport";
import GetTransactionStatus from "./Forms/getTransactionStatus";
import GetVariables from "./Forms/getVariables";
import InstallCertificate from "./Forms/installCertificate";
import PublishFirmware from "./Forms/publishFirmware";
import ReserveNow from "./Forms/reserveNow";
import Reset from "./Forms/reset";
import SendLocalList from "./Forms/sendLocalList";
import SetChargingProfile from "./Forms/setChargingProfile";
import SetDisplayMessage from "./Forms/setDisplayMessage";
import SetMonitoringBase from "./Forms/setMonitoringBase";
import SetMonitoringLevel from "./Forms/setMonitoringLevel";
import SetNetworkProfile from "./Forms/setNetworkProfile";
import SetVariableMonitoring from "./Forms/setVariableMonitoring";
import SetVariables from "./Forms/setVariables";
import SignCertificate from "./Forms/signCertificate";
import TriggerMessage from "./Forms/triggerMessage";
import UnlockConnector from "./Forms/unlockConnector";
import UnpublishFirmware from "./Forms/unpublishFirmware";
import UpdateFirmware from "./Forms/updateFirmware";

export class MainForm extends Component {
    constructor(props) {
        super(props);
        this.state = {
            chargers: [],
            charger: "",
            isLoaded: false,
            error: null,
        };
        this.handleChange = this.handleChange.bind(this);
    }

    componentDidMount() {
        axios.get(`http://${IP_AND_PORT}/api/get-chargers`).then(res => {
                this.setState({
                    isLoaded: true,
                    chargers: res.data,
                    charger: res.data.length > 1 ? res.data[0] : ""
                });
            },
                (error) => {
                    this.setState({
                        isLoaded: true,
                        error
                    });
                })
    }

    handleChange(event) {
            this.setState({charger: event.target.value})
    }

    render() {
        const { chargers, isLoaded, error } = this.state;
        if (error) {
            return <Alert variant="danger">{error.message}</Alert>;
        } else if (!isLoaded) {
            return <Alert variant="info">Loading...</Alert>;
        } else {
            return (
                <>
                    <Form>
                        <Form.Group controlId={"formSelectCharger"}>
                            <Form.Label style={{color: "white"}}>Select charger:&nbsp;</Form.Label>
                            <Form.Control as={"select"} onChange={this.handleChange} >
                                {chargers.map((charger) => <option value={charger} key={charger}>{charger}</option>)}
                            </Form.Control>
                        </Form.Group>
                    </Form>
                    <h5 style={{color: "white"}}>Select request:</h5>
                    <Accordion>
                        <CancelReservation charger={ this.state.charger } title="Cancel reservation request" eventKey="0" uid={this.props.uid}/>
                        <CertificateSigned charger={this.state.charger} title="Certificate signed" eventKey="1" uid={this.props.uid}/>
                        <ChangeAvailability charger={this.state.charger} title="Change availability" eventKey="2" uid={this.props.uid}/>
                        <ClearCache charger={this.state.charger} title="Clear cache" eventKey="3" uid={this.props.uid}/>
                        <ClearChargingProfile charger={this.state.charger} title="Clear charging profile" eventKey="4" uid={this.props.uid}/>
                        <ClearDisplayMessage charger={this.state.charger} title="Clear display message" eventKey="5" uid={this.props.uid}/>
                        <ClearVariableMonitoring charger={this.state.charger} title="Clear variable monitoring" eventKey="6"  uid={this.props.uid}/>
                        <CostUpdated charger={this.state.charger} title="Cost updated" eventKey="7" uid={this.props.uid}/>
                        <CustomerInformation charger={this.state.charger} title="Customer information" eventKey="8" uid={this.props.uid}/>
                        <DataTransfer charger={this.state.charger} title="Data transfer" eventKey="9" uid={this.props.uid}/>
                        <DeleteCertificate charger={this.state.charger} title="Delete certificate" eventKey="10" uid={this.props.uid}/>
                        <GetBaseReport charger={this.state.charger} title="Get base report" eventKey="11" uid={this.props.uid}/>
                        <GetChargingProfiles charger={this.state.charger} title="Get charging profiles" eventKey="12" uid={this.props.uid}/>
                        <GetCompositeSchedule charger={this.state.charger} title="Get composite schedule" eventKey="13" uid={this.props.uid}/>
                        <GetDisplayMessages charger={this.state.charger} title="Get display messages" eventKey="14" uid={this.props.uid}/>
                        <GetInstalledCertificateIds charger={this.state.charger} title="Get installed certificate Id's" eventKey="15" uid={this.props.uid}/>
                        <GetLocalListVersion charger={this.state.charger} title="Get local list version" eventKey="16" uid={this.props.uid}/>
                        <GetLog charger={this.state.charger} title="Get log" eventKey="17" uid={this.props.uid}/>
                        <GetMonitoringReport charger={this.state.charger} title="Get monitoring report" eventKey="18" uid={this.props.uid}/>
                        <GetReport charger={this.state.charger} title="Get report" eventKey="19" uid={this.props.uid}/>
                        <GetTransactionStatus charger={this.state.charger} title="Get transaction status" eventKey="20" uid={this.props.uid}/>
                        <GetVariables charger={this.state.charger} title="Get variables" eventKey="21" uid={this.props.uid}/>
                        <InstallCertificate charger={this.state.charger} title="Install certificate" eventKey="22" uid={this.props.uid}/>
                        <PublishFirmware charger={this.state.charger} title="Publish firmware" eventKey="23" uid={this.props.uid}/>
                        <ReserveNow charger={this.state.charger} title="Reserve now" eventKey="24" uid={this.props.uid}/>
                        <Reset charger={this.state.charger} title="Reset" eventKey="25" uid={this.props.uid}/>
                        <SendLocalList charger={this.state.charger} title="Send local list" eventKey="26" uid={this.props.uid}/>
                        <SetChargingProfile charger={this.state.charger} title="Set charging profile" eventKey="27" uid={this.props.uid}/>
                        <SetDisplayMessage charger={this.state.charger} title="Set display message" eventKey="28" uid={this.props.uid}/>
                        <SetMonitoringBase charger={this.state.charger} title="Set monitoring base" eventKey="29" uid={this.props.uid}/>
                        <SetMonitoringLevel charger={this.state.charger} title="Set monitoring level" eventKey="30" uid={this.props.uid}/>
                        <SetNetworkProfile charger={this.state.charger} title="Set network profile" eventKey="31" uid={this.props.uid}/>
                        <SetVariableMonitoring charger={this.state.charger} title="Set variable monitoring" eventKey="32" uid={this.props.uid}/>
                        <SetVariables charger={this.state.charger} title="Set variables" eventKey="33" uid={this.props.uid}/>
                        <SignCertificate charger={this.state.charger} title="Sign certificate" eventKey="34" uid={this.props.uid}/>
                        <TriggerMessage charger={this.state.charger} title="Trigger Message" eventKey="35" uid={this.props.uid}/>
                        <UnlockConnector charger={this.state.charger} title="Unlock connector" eventKey="36" uid={this.props.uid}/>
                        <UnpublishFirmware charger={this.state.charger} title="Unpublish firmware" eventKey="37" uid={this.props.uid}/>
                        <UpdateFirmware charger={this.state.charger} title="Update firmware" eventKey="38" uid={this.props.uid}/>
                    </Accordion>
                </>

            );
        }

    }
}

MainForm.propTypes = {
    uid: PropTypes.string.isRequired
}

export default MainForm;