import _common from "./_common";
import axios from "axios";
import {IP_AND_PORT} from "../../config";
export class ChangeAvailability extends _common {
    requestName = "ChangeAvailability";
    schema = {
        definitions: {
            CustomDataType: {
                description: "This class does not get 'AdditionalProperties = false' in the schema generation, so it can be extended with arbitrary JSON properties to allow adding custom data.",
                type: "object",
                properties: {
                    jsonString: {
                        title: "JSON String",
                        description: "If this field is not changed, the whole customData property will be removed",
                        type: "string",
                        default: "{\"vendorId\": \"\"}",
                        maxLength: 10000
                    }
                }
            },
            OperationalStatusEnumType: {
                description: "This contains the type of availability change that the Charging Station should perform.",
                type: "string",
                additionalProperties: false,
                enum: ["Inoperative", "Operative"]
            },
            EVSEType: {
                description: "Electric Vehicle Supply Equipment",
                type: "object",
                additionalProperties: false,
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    id: {
                        description: "EVSE Identifier. This contains a number > 0 designating an EVSE of the Charging Station.",
                        type: "integer"
                    },
                    connectorId: {
                        description: "An id to designate a specific connector (on an EVSE) by connector index number.",
                        type: "integer"
                    }
                },
                required: ["id"]
            }
        },
        type: "object",
        properties: {
            customData: {
                $ref: "#/definitions/CustomDataType"
            },
            evse: {
                $ref: "#/definitions/EVSEType"
            },
            operationalStatus: {
                "$ref": "#/definitions/OperationalStatusEnumType"
            }
        },
        required: ["operationalStatus"]
    }

    validate = (formData, errors) => {
        let json = null;
        if(formData.customData.jsonString.length > 0){
            try {
                json = JSON.parse(formData.customData.jsonString);
                if(!json.hasOwnProperty("vendorId")){
                    errors.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                }
            } catch (e) {
                errors.customData.jsonString.addError("cannot parse this field into JSON string");
                return errors;
            }
        }
        if(formData.evse.customData.jsonString.length > 0){
            try {
                json = JSON.parse(formData.evse.customData.jsonString);
                if(!json.hasOwnProperty("vendorId")){
                    errors.evse.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                }
            } catch (e) {
                errors.evse.customData.jsonString.addError("cannot parse this field into JSON string");
                return errors;
            }
        }
        return errors;
    }

    onSubmit = async ({formData}) => {
        formData = this.formatCustomData(formData);
        formData.evse = this.formatCustomData(formData.evse);
        const json = this.formatData(formData);
        window.alert(json);
        await axios.post(`http://${IP_AND_PORT}/api/post-request`,
             json, {headers: {'Content-Type': 'application/json'}});
    }

}

export default ChangeAvailability;