import _common from './_common';
import axios from "axios";
import {IP_AND_PORT} from "../../config";

class SetChargingProfile extends _common {
    requestName = "SetChargingProfile";
    schema = {
        definitions: {
            CustomDataType: {
                description: "This class can be extended with arbitrary JSON properties to allow adding custom data.",
                type: "object",
                properties: {
                    jsonString: {
                        title: "JSON String",
                        description: "If this field is not changed, the whole customData property will be removed",
                        type: "string",
                        default: "{\"vendorId\": \"\"}",
                        maxLength: 10000
                    }
                }
            },
            ChargingProfileKindEnumType: {
                description: "Indicates the kind of schedule.",
                type: "string",
                enum: ["Absolute", "Recurring", "Relative"]
            },
            ChargingProfilePurposeEnumType: {
                description: "Defines the purpose of the schedule transferred by this profile",
                type: "string",
                enum: ["ChargingStationExternalConstraints", "ChargingStationMaxProfile", "TxDefaultProfile",
                    "TxProfile"]
            },
            ChargingRateUnitEnumType: {
                description: "The unit of measure Limit is expressed in.",
                type: "string",
                enum: ["W", "A"]
            },
            CostKindEnumType: {
                description: "The kind of cost referred to in the message element amount",
                type: "string",
                enum: ["CarbonDioxideEmission", "RelativePricePercentage", "RenewableGenerationPercentage"]
            },
            RecurrencyKindEnumType: {
                description: "Indicates the start point of a recurrence.",
                type: "string",
                enum: ["Daily", "Weekly"]
            },
            ChargingProfileType: {
                description: "A ChargingProfile consists of ChargingSchedule, describing the amount of power or " +
                    "current that can be delivered per time interval.",
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    id: {
                        description: "Id of ChargingProfile.",
                        type: "integer"
                    },
                    stackLevel: {
                        description: "Value determining level in hierarchy stack of profiles. Higher values have " +
                            "precedence over lower values. Lowest level is 0.",
                        type: "integer"
                    },
                    chargingProfilePurpose: {
                        $ref: "#/definitions/ChargingProfilePurposeEnumType"
                    },
                    chargingProfileKind: {
                        $ref: "#/definitions/ChargingProfileKindEnumType"
                    },
                    recurrencyKind: {
                        $ref: "#/definitions/RecurrencyKindEnumType"
                    },
                    validFrom: {
                        description: "Point in time at which the profile starts to be valid. If absent, the profile " +
                            "is valid as soon as it is received by the Charging Station.",
                        type: "string",
                        format: "date-time"
                    },
                    validTo: {
                        description: "Point in time at which the profile stops to be valid. If absent, the profile " +
                            "is valid until it is replaced by another profile.",
                        type: "string",
                        format: "date-time"
                    },
                    chargingSchedule: {
                        type: "array",
                        items: {
                            $ref: "#/definitions/ChargingScheduleType"
                        },
                        minItems: 1,
                        maxItems: 3
                    },
                    transactionId: {
                        description: "SHALL only be included if ChargingProfilePurpose is set to TxProfile. The " +
                            "transactionId is used to match the profile to a specific transaction.",
                        type: "string",
                        maxLength: 36
                    }
                },
                required: ["id", "stackLevel", "chargingProfilePurpose", "chargingProfileKind", "chargingSchedule"]
            },
            ChargingSchedulePeriodType: {
                description: "Charging schedule period structure defines a time period in a charging schedule.",
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    startPeriod: {
                        description: "Start of the period, in seconds from the start of schedule. The value of " +
                            "StartPeriod also defines the stop time of the previous period.",
                        type: "integer"
                    },
                    limit: {
                        description: "Charging rate limit during the schedule period, in the applicable " +
                            "chargingRateUnit, for example in Amperes (A) or Watts (W). Accepts at most one digit " +
                            "fraction (e.g. 8.1).",
                        type: "number"
                    },
                    numberPhases: {
                        description: "The number of phases that can be used for charging. If a number of phases is " +
                            "needed, numberPhases=3 will be assumed unless another number is given.\r\n",
                        type: "integer"
                    },
                    phaseToUse: {
                        description: "Values: 1..3, Used if numberPhases=1 and if the EVSE is capable of switching " +
                            "the phase connected to the EV, i.e. ACPhaseSwitchingSupported is defined and true. " +
                            "It's not allowed unless both conditions above are true. If both conditions are true, " +
                            "and phaseToUse is omitted, the Charging Station / EVSE will make the selection on its own.",
                        type: "integer"
                    }
                },
                required: ["startPeriod", "limit"]
            },
            ChargingScheduleType: {
                description: "Charging schedule structure defines a list of charging periods, as used in: " +
                    "GetCompositeSchedule.conf and ChargingProfile.",
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    id: {
                        description: "Identifies the ChargingSchedule.",
                        type: "integer"
                    },
                    startSchedule: {
                        description: "Starting point of an absolute schedule. If absent the schedule will be " +
                            "relative to start of charging.",
                        type: "string",
                        format: "date-time"
                    },
                    duration: {
                        description: "Duration of the charging schedule in seconds. If the duration is left empty, " +
                            "the last period will continue indefinitely or until end of the transaction if " +
                            "chargingProfilePurpose = TxProfile.",
                        type: "integer"
                    },
                    chargingRateUnit: {
                        $ref: "#/definitions/ChargingRateUnitEnumType"
                    },
                    chargingSchedulePeriod: {
                        type: "array",
                        items: {
                            $ref: "#/definitions/ChargingSchedulePeriodType"
                        },
                        minItems: 1,
                        maxItems: 1024
                    },
                    minChargingRate: {
                        description: "Minimum charging rate supported by the EV. The unit of measure is defined " +
                            "by the chargingRateUnit. This parameter is intended to be used by a local smart " +
                            "charging algorithm to optimize the power allocation for in the case a charging process " +
                            "is inefficient at lower charging rates. Accepts at most one digit fraction (e.g. 8.1)",
                        type: "number"
                    },
                    salesTariff: {
                        $ref: "#/definitions/SalesTariffType"
                    }
                },
                required: ["id", "chargingRateUnit", "chargingSchedulePeriod"]
            },
            ConsumptionCostType: {
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    startValue: {
                        description: "The lowest level of consumption that defines the starting point of this " +
                            "consumption block. The block interval extends to the start of the next interval.",
                        type: "number"
                    },
                    cost: {
                        type: "array",
                        items: {
                            $ref: "#/definitions/CostType"
                        },
                        minItems: 1,
                        maxItems: 3
                    }
                },
                required: ["startValue", "cost"]
            },
            CostType: {
                description: "Cost",
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    costKind: {
                        $ref: "#/definitions/CostKindEnumType"
                    },
                    amount: {
                        description: "The estimated or actual cost per kWh",
                        type: "integer"
                    },
                    amountMultiplier: {
                        description: "Values: -3..3, The amountMultiplier defines the exponent to base 10 (dec). " +
                            "The final value is determined by: amount * 10 ^ amountMultiplier",
                        type: "integer"
                    }
                },
                required: ["costKind", "amount"]
            },
            RelativeTimeIntervalType: {
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    start: {
                        description: "Start of the interval, in seconds from NOW.",
                        type: "integer"
                    },
                    duration: {
                        description: "Duration of the interval, in seconds.",
                        type: "integer"
                    }
                },
                required: ["start"]
            },
            SalesTariffEntryType: {
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    relativeTimeInterval: {
                        $ref: "#/definitions/RelativeTimeIntervalType"
                    },
                    ePriceLevel: {
                        description: "Defines the price level of this SalesTariffEntry (referring to NumEPriceLevels). " +
                            "Small values for the EPriceLevel represent a cheaper TariffEntry. Large values for the " +
                            "EPriceLevel represent a more expensive TariffEntry.",
                        type: "integer",
                        minimum: 0.0
                    },
                    consumptionCost: {
                        type: "array",
                        items: {
                            $ref: "#/definitions/ConsumptionCostType"
                        },
                        minItems: 1,
                        maxItems: 3
                    }
                },
                required: ["relativeTimeInterval"]
            },
            SalesTariffType: {
                description: "NOTE: This dataType is based on dataTypes from <<ref-ISOIEC15118-2,ISO 15118-2>>.",
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    id: {
                        description: "SalesTariff identifier used to identify one sales tariff. An SAID remains a " +
                            "unique identifier for one schedule throughout a charging session.",
                        type: "integer"
                    },
                    salesTariffDescription: {
                        description: "A human readable title/short description of the sales tariff e.g. for HMI display purposes.",
                        type: "string",
                        maxLength: 32
                    },
                    numEPriceLevels: {
                        description: "Defines the overall number of distinct price levels used across all provided " +
                            "SalesTariff elements.\r\n",
                        type: "integer"
                    },
                    salesTariffEntry: {
                        type: "array",
                        items: {
                            $ref: "#/definitions/SalesTariffEntryType"
                        },
                        minItems: 1,
                        maxItems: 1024
                    }
                },
                required: ["id", "salesTariffEntry"]
            }
        },
        type: "object",
        properties: {
            customData: {
                $ref: "#/definitions/CustomDataType"
            },
            evseId: {
                description: "For TxDefaultProfile an evseId=0 applies the profile to each individual evse. " +
                    "For ChargingStationMaxProfile and ChargingStationExternalConstraints an evseId=0 contains an " +
                    "overall limit for the whole Charging Station.",
                type: "integer"
            },
            chargingProfile: {
                $ref: "#/definitions/ChargingProfileType"
            }
        }
    }

    validate = (formData, errors) => {
        let json = null;
        if (formData.customData.jsonString.length > 0) {
            try {
                json = JSON.parse(formData.customData.jsonString);
                if(!json.hasOwnProperty("vendorId")){
                    errors.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                }
            } catch {
                errors.customData.jsonString.addError("cannot parse this field into JSON string");
            }
        }
        if (formData.chargingProfile.customData.jsonString.length > 0) {
            try {
                json = JSON.parse(formData.chargingProfile.customData.jsonString);
                if(!json.hasOwnProperty("vendorId")){
                    errors.chargingProfile.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                }
            } catch {
                errors.chargingProfile.customData.jsonString.addError("cannot parse this field into JSON string");
            }
        }
        formData.chargingProfile.chargingSchedule.forEach((value_a, index_a) => {
            if (value_a.customData.jsonString.length > 0) {
                try {
                    json = JSON.parse(value_a.customData.jsonString);
                    if(!json.hasOwnProperty("vendorId")){
                        errors.chargingProfile.chargingSchedule[index_a].customData.jsonString.addError("JSON string should have \"vendorId\" property");
                    }
                } catch {
                    errors.chargingProfile.chargingSchedule[index_a].customData.jsonString.addError("cannot parse this field into JSON string");
                }
            }
            value_a.chargingSchedulePeriod.forEach((value_b, index_b) => {
                if (value_b.customData.jsonString.length > 0) {
                    try {
                        json = JSON.parse(value_b.customData.jsonString);
                        if(!json.hasOwnProperty("vendorId")){
                            errors.chargingProfile.chargingSchedule[index_a].chargingSchedulePeriod[index_b].customData.jsonString.addError("JSON string should have \"vendorId\" property");
                        }
                    } catch {
                        errors.chargingProfile.chargingSchedule[index_a].chargingSchedulePeriod[index_b].customData.jsonString.addError("cannot parse this field into JSON string");
                    }
                }
            });
            if (value_a.salesTariff.customData.jsonString.length > 0) {
                try {
                    json = JSON.parse(value_a.salesTariff.customData.jsonString);
                    if(!json.hasOwnProperty("vendorId")){
                        errors.chargingProfile.chargingSchedule[index_a].salesTariff.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                    }
                } catch {
                    errors.chargingProfile.chargingSchedule[index_a].salesTariff.customData.jsonString.addError("cannot parse this field into JSON string");
                }
            }
            value_a.salesTariff.salesTariffEntry.forEach((value_b, index_b) => {
                if (value_b.customData.jsonString.length > 0) {
                    try {
                        json = JSON.parse(value_b.customData.jsonString);
                        if(!json.hasOwnProperty("vendorId")){
                            errors.chargingProfile.chargingSchedule[index_a].salesTariff.salesTariffEntry[index_b].customData.jsonString.addError("JSON string should have \"vendorId\" property");
                        }
                    } catch {
                        errors.chargingProfile.chargingSchedule[index_a].salesTariff.salesTariffEntry[index_b].customData.jsonString.addError("cannot parse this field into JSON string");
                    }
                }
                if (value_b.relativeTimeInterval.customData.jsonString.length > 0) {
                    try {
                        json = JSON.parse(value_b.relativeTimeInterval.customData.jsonString);
                        if(!json.hasOwnProperty("vendorId")){
                            errors.chargingProfile.chargingSchedule[index_a].salesTariff.salesTariffEntry[index_b].relativeTimeInterval.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                        }
                    } catch {
                        errors.chargingProfile.chargingSchedule[index_a].salesTariff.salesTariffEntry[index_b].relativeTimeInterval.customData.jsonString.addError("cannot parse this field into JSON string");
                    }
                }
                value_b.consumptionCost.forEach((value_c, index_c) => {
                    if (value_c.customData.jsonString.length > 0) {
                        try {
                            json = JSON.parse(value_c.customData.jsonString);
                            if(!json.hasOwnProperty("vendorId")){
                                errors.chargingProfile.chargingSchedule[index_a].salesTariff.salesTariffEntry[index_b].consumptionCost[index_c].customData.jsonString.addError("JSON string should have \"vendorId\" property");
                            }
                        } catch {
                            errors.chargingProfile.chargingSchedule[index_a].salesTariff.salesTariffEntry[index_b].consumptionCost[index_c].customData.jsonString.addError("cannot parse this field into JSON string");
                        }
                    }
                    value_c.cost.forEach((value_d, index_d) => {
                        if (value_d.customData.jsonString.length > 0) {
                            try {
                                json = JSON.parse(value_d.customData.jsonString);
                                if(!json.hasOwnProperty("vendorId")){
                                    errors.chargingProfile.chargingSchedule[index_a].salesTariff.salesTariffEntry[index_b].consumptionCost[index_c].cost[index_d].customData.jsonString.addError("JSON string should have \"vendorId\" property");
                                }
                            } catch {
                                errors.chargingProfile.chargingSchedule[index_a].salesTariff.salesTariffEntry[index_b].consumptionCost[index_c].cost[index_d].customData.jsonString.addError("cannot parse this field into JSON string");
                            }
                        }
                    });
                });
            });
        });
        return errors;
    }

    onSubmit = async ({formData}) => {
        formData = this.formatCustomData(formData);
        formData.chargingProfile = this.formatCustomData(formData.chargingProfile);
        formData.chargingProfile.chargingSchedule.forEach((value_a, index_a) => {
            formData.chargingProfile.chargingSchedule[index_a] = this.formatCustomData(value_a);
            formData.chargingProfile.chargingSchedule[index_a].chargingSchedulePeriod.forEach((value_b, index_b) => {
                formData.chargingProfile.chargingSchedule[index_a].chargingSchedulePeriod[index_b] = this.formatCustomData(value_b);
            });
            formData.chargingProfile.chargingSchedule[index_a].salesTariff = this.formatCustomData(value_a.salesTariff);
            formData.chargingProfile.chargingSchedule[index_a].salesTariff.salesTariffEntry.forEach((value_b, index_b) => {
                formData.chargingProfile.chargingSchedule[index_a].salesTariff.salesTariffEntry[index_b] = this.formatCustomData(value_b);
                formData.chargingProfile.chargingSchedule[index_a].salesTariff.salesTariffEntry[index_b].relativeTimeInterval = this.formatCustomData(value_b.relativeTimeInterval);
                formData.chargingProfile.chargingSchedule[index_a].salesTariff.salesTariffEntry[index_b].consumptionCost.forEach((value_c, index_c) => {
                    formData.chargingProfile.chargingSchedule[index_a].salesTariff.salesTariffEntry[index_b].consumptionCost[index_c] = this.formatCustomData(value_c);
                    formData.chargingProfile.chargingSchedule[index_a].salesTariff.salesTariffEntry[index_b].consumptionCost[index_c].cost.forEach((value_d, index_d) => {
                        formData.chargingProfile.chargingSchedule[index_a].salesTariff.salesTariffEntry[index_b].consumptionCost[index_c].cost[index_d] = this.formatCustomData(value_d);
                    });
                })
            });
        });
        const json = this.formatData(formData);
        window.alert(json);
        await axios.post(`http://${IP_AND_PORT}/api/post-request`,
             json, {headers: {'Content-Type': 'application/json'}});
    }
}

export default SetChargingProfile;