import _common from './_common';
import axios from "axios";
import {IP_AND_PORT} from "../../config";

class GetVariables extends _common {
    requestName = "GetVariables";
    schema = {
        definitions: {
            CustomDataType: {
                description: "This class can be extended with arbitrary JSON properties to allow adding custom data.",
                type: "object",
                properties: {
                    jsonString: {
                        title: "JSON String",
                        description: "If this field is not changed, the whole customData property will be removed",
                        type: "string",
                        default: "{\"vendorId\": \"\"}",
                        maxLength: 10000
                    }
                }
            },
            AttributeEnumType: {
                description: "Attribute type for which value is requested. When absent, default Actual is assumed.",
                type: "string",
                default: "Actual",
                enum: ["Actual", "Target", "MinSet", "MaxSet"]
            },
            ComponentType: {
                description: "A physical or logical component",
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    evse: {
                        $ref: "#/definitions/EVSEType"
                    },
                    name: {
                        description: "Name of the component. Name should be taken from the list of standardized component names whenever possible. Case Insensitive. strongly advised to use Camel Case.",
                        type: "string",
                        maxLength: 50
                    },
                    instance: {
                        description: "Name of instance in case the component exists as multiple instances. Case Insensitive. strongly advised to use Camel Case.",
                        type: "string",
                        maxLength: 50
                    }
                },
                required: ["name"]
            },
            EVSEType: {
                description: "Electric Vehicle Supply Equipment",
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    id: {
                        description: "EVSE Identifier. This contains a number > 0 designating an EVSE of the Charging Station.",
                        type: "integer"
                    },
                    connectorId: {
                        description: "An id to designate a specific connector (on an EVSE) by connector index number.",
                        type: "integer"
                    }
                },
                required: ["id"]
            },
            GetVariableDataType: {
                description: "Class to hold parameters for GetVariables request.",
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    attributeType: {
                        $ref: "#/definitions/AttributeEnumType"
                    },
                    component: {
                        $ref: "#/definitions/ComponentType"
                    },
                    variable: {
                        $ref: "#/definitions/VariableType"
                    }
                },
                required: ["component", "variable"]
            },
            VariableType: {
                description: "Reference key to a component-variable.",
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    name: {
                        description: "Name of the variable. Name should be taken from the list of standardized variable names whenever possible. Case Insensitive. strongly advised to use Camel Case.",
                        type: "string",
                        maxLength: 50
                    },
                    instance: {
                        description: "Name of instance in case the variable exists as multiple instances. Case Insensitive. strongly advised to use Camel Case.",
                        type: "string",
                        maxLength: 50
                    }
                },
                required: ["name"]
            }
        },
        type: "object",
        properties: {
            customData: {
                $ref: "#/definitions/CustomDataType"
            },
            getVariableData: {
                type: "array",
                items: {
                    $ref: "#/definitions/GetVariableDataType"
                },
                minItems: 1
            }
        },
        required: ["getVariableData"]
    }

    validate = (formData, errors) => {
        let json = null;
        if (formData.hasOwnProperty("customData")) {
            if (formData.customData.hasOwnProperty("jsonString")) {
                if (formData.customData.jsonString.length > 0) {
                    try {
                        json = JSON.parse(formData.customData.jsonString);
                        if(!json.hasOwnProperty("vendorId")){
                            errors.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                        }
                    } catch {
                        errors.customData.jsonString.addError("cannot parse this field into JSON string");
                    }
                }

            }
        }
        if (formData.hasOwnProperty("getVariableData")) {
            formData.getVariableData.forEach((value, index) => {
                console.log(errors.getVariableData[index]);
                try {
                    json = JSON.parse(value.customData.jsonString);
                    if(!json.hasOwnProperty("vendorId")){
                        errors.getVariableData[index].customData.jsonString.addError("JSON string should have \"vendorId\" property");
                    }
                } catch {
                    // Uncaught TypeError: Cannot read property 'jsonString' of undefined

                    errors.getVariableData[index].customData.jsonString.addError("cannot parse this field into JSON string")
                }
                try {
                    json = JSON.parse(value.attributeType.customData.jsonString);
                    if(!json.hasOwnProperty("vendorId")){
                        errors.getVariableData[index].attributeType.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                    }
                } catch {
                    errors.getVariableData[index].attributeType.customData.jsonString.addError("cannot parse this field into JSON string")
                }
                try {
                    json = JSON.parse(value.component.customData.jsonString);
                    if(!json.hasOwnProperty("vendorId")){
                        errors.getVariableData[index].component.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                    }
                } catch {
                    errors.getVariableData[index].component.customData.jsonString.addError("cannot parse this field into JSON string")
                }
                try {
                    json = JSON.parse(value.component.evse.customData.jsonString);
                    if(!json.hasOwnProperty("vendorId")){
                        errors.getVariableData[index].component.evse.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                    }
                } catch {
                    errors.getVariableData[index].component.evse.customData.jsonString.addError("cannot parse this field into JSON string")
                }
                try {
                    json = JSON.parse(value.variable.customData.jsonString);
                    if(!json.hasOwnProperty("vendorId")){
                        errors.getVariableData[index].variable.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                    }
                } catch {
                    errors.getVariableData[index].variable.customData.jsonString.addError("cannot parse this field into JSON string")
                }

            })
        }
        return errors;
    }

    onSubmit = async ({formData}) => {
        formData = this.formatCustomData(formData);
        formData.componentVariable.forEach((value, index) => {
            formData.componentVariable[index] = this.formatCustomData(value);
            formData.componentVariable[index].attributeType = this.formatCustomData(value.attributeType);
            formData.componentVariable[index].component = this.formatCustomData(value.component);
            formData.componentVariable[index].component.evse = this.formatCustomData(value.component.evse);
            formData.componentVariable[index].variable = this.formatCustomData(value.variable);
        });
        const json = this.formatData(formData);
        window.alert(json);
        await axios.post(`http://${IP_AND_PORT}/api/post-request`,
             json, {headers: {'Content-Type': 'application/json'}});
    }
}

export default GetVariables;