import _common from "./_common";
import axios from "axios";
import {IP_AND_PORT} from "../../config";

class GetChargingProfiles extends _common {
    requestName="GetChargingProfiles";
    schema = {
        definitions: {
            CustomDataType: {
                description: "This class can be extended with arbitrary JSON properties to allow adding custom data.",
                type: "object",
                properties: {
                    jsonString: {
                        title: "JSON String",
                        description: "If this field is not changed, the whole customData property will be removed",
                        type: "string",
                        default: "{\"vendorId\": \"\"}",
                        maxLength: 10000
                    }
                }
            },
            ChargingLimitSourceEnumType: {
                type: "string",
                enum: ["EMS", "Other", "SO", "CSO"]
            },
            ChargingProfilePurposeEnumType: {
                description: "Defines the purpose of the schedule transferred by this profile",
                type: "string",
                enum: ["ChargingStationExternalConstraints", "ChargingStationMaxProfile", "TxDefaultProfile", "TxProfile"]
            },
            ChargingProfileCriterionType: {
                description: "A ChargingProfile consists of ChargingSchedule, describing the amount of power or " +
                    "current that can be delivered per time interval.",
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    chargingProfilePurpose: {
                        $ref: "#/definitions/ChargingProfilePurposeEnumType"
                    },
                    stackLevel: {
                        description: "Value determining level in hierarchy stack of profiles. Higher values have " +
                            "precedence over lower values. Lowest level is 0.",
                        type: "integer"
                    },
                    chargingProfileId: {
                        description: "List of all the chargingProfileIds requested. Any ChargingProfile that matches " +
                            "one of these profiles will be reported. If omitted, the Charging Station SHALL not filter" +
                            " on chargingProfileId. This field SHALL NOT contain more ids than set in " +
                            "<<configkey-charging-profile-entries,ChargingProfileEntries.maxLimit>>",
                        type: "array",
                        items: {
                            type: "integer"
                        },
                        minItems: 1
                    },
                    chargingLimitSource: {
                        description: "For which charging limit sources, charging profiles SHALL be reported. " +
                            "If omitted, the Charging Station SHALL not filter on chargingLimitSource.",
                        type: "array",
                        items: {
                            $ref: "#/definitions/ChargingLimitSourceEnumType"
                        },
                        minItems: 1,
                        maxItems: 4
                    }
                }
            }

        },
        type: "object",
        properties: {
            customData: {
                $ref: "#/definitions/CustomDataType"
            },
            requestId: {
                description: "Reference identification that is to be used by the Charging Station in the &lt;&lt;reportchargingprofilesrequest, ReportChargingProfilesRequest&gt;&gt; when provided.\r\n",
                type: "integer"
            },
            evseId: {
                description: "For which EVSE installed charging profiles SHALL be reported. If 0, only charging profiles installed on the Charging Station itself (the grid connection) SHALL be reported. If omitted, all installed charging profiles SHALL be reported.\r\n",
                type: "integer"
            },
            chargingProfile: {
                $ref: "#/definitions/ChargingProfileCriterionType"
            }
        },
        required: ["requestId", "chargingProfile"]
    };

    validate = (formData, errors) => {
        let json = null;
        if(formData.customData.jsonString.length > 0){
            try {
                json = JSON.parse(formData.customData.jsonString);
                if(!json.hasOwnProperty("vendorId")){
                    errors.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                }
            } catch {
                errors.customData.jsonString.addError("cannot parse this field into JSON string");
            }
        }
        if(formData.chargingProfile.customData.jsonString.length > 0){
            try {
                json = JSON.parse(formData.chargingProfile.customData.jsonString);
                if(!json.hasOwnProperty("vendorId")){
                    errors.chargingProfile.customData.jsonString.addError("JSON string should have \"vendorId\" property")
                }
            } catch {
                errors.chargingProfile.customData.jsonString.addError("cannot parse this field into JSON string");
            }
        }
        return errors;
    }

    onSubmit = async ({formData}) => {
        formData = this.formatCustomData(formData);
        const json = this.formatData(formData);
        window.alert(json);
        await axios.post(`http://${IP_AND_PORT}/api/post-request`,
             json, {headers: {'Content-Type': 'application/json'}});
    }
}

export default GetChargingProfiles;