import _common from './_common';
import axios from "axios";
import {IP_AND_PORT} from "../../config";

class SendLocalList extends _common {
    requestName = "SendLocalList";
    schema = {
        definitions: {
            CustomDataType: {
                description: "This class can be extended with arbitrary JSON properties to allow adding custom data.",
                type: "object",
                properties: {
                    jsonString: {
                        title: "JSON String",
                        description: "If this field is not changed, the whole customData property will be removed",
                        type: "string",
                        default: "{\"vendorId\": \"\"}",
                        maxLength: 10000
                    }
                }
            },
            AuthorizationStatusEnumType: {
                description: "Current status of the ID Token.",
                type: "string",
                enum: ["Accepted", "Blocked", "ConcurrentTx", "Expired", "Invalid", "NoCredit", "NotAllowedTypeEVSE",
                    "NotAtThisLocation", "NotAtThisTime", "Unknown"]
            },
            IdTokenEnumType: {
                description: "Enumeration of possible idToken types.",
                type: "string",
                enum: ["Central", "eMAID", "ISO14443", "ISO15693", "KeyCode", "Local", "MacAddress", "NoAuthorization"]
            },
            MessageFormatEnumType: {
                description: "Format of the message.",
                type: "string",
                enum: ["ASCII", "HTML", "URI", "UTF8"]
            },
            UpdateEnumType: {
                description: "This contains the type of update (full or differential) of this request.",
                type: "string",
                enum: ["Differential", "Full"]
            },
            AdditionalInfoType: {
                description: "Contains a case insensitive identifier to use for the authorization and the type of authorization to support multiple forms of identifiers.",
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    additionalIdToken: {
                        description: "This field specifies the additional IdToken.\r\n",
                        type: "string",
                        maxLength: 36
                    },
                    type: {
                        description: "This defines the type of the additionalIdToken. This is a custom type, so the implementation needs to be agreed upon by all involved parties.\r\n",
                        type: "string",
                        maxLength: 50
                    }
                },
                required: ["additionalIdToken", "type"]
            },
            AuthorizationData: {
                description: "Contains the identifier to use for authorization.",
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    idToken: {
                        $ref: "#/definitions/IdTokenType"
                    },
                    idTokenInfo: {
                        $ref: "#/definitions/IdTokenInfoType"
                    }
                },
                required: ["idToken"]
            },
            IdTokenInfoType: {
                description: "Contains status information about an identifier.\r\nIt is advised to not stop charging for a token that expires during charging, as ExpiryDate is only used for caching purposes. If ExpiryDate is not given, the status has no end date.",
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    status: {
                        $ref: "#/definitions/AuthorizationStatusEnumType"
                    },
                    cacheExpiryDateTime: {
                        description: "Date and Time after which the token must be considered invalid.",
                        type: "string",
                        format: "date-time"
                    },
                    chargingPriority: {
                        description: "Priority from a business point of view. Default priority is 0, The range is from -9 to 9. Higher values indicate a higher priority. The chargingPriority in <<TransactionEventResponse>> overrules this one. ",
                        type: "integer",
                        minimum: -9,
                        maximum: 9
                    },
                    language1: {
                        description: "Preferred user interface language of identifier user. Contains a language code as defined in <<ref-RFC5646,[RFC5646]>>.",
                        type: "string",
                        maxLength: 8
                    },
                    evseId: {
                        description: "Only used when the IdToken is only valid for one or more specific EVSEs, not for the entire Charging Station.",
                        type: "array",
                        items: {
                            type: "integer"
                        },
                        minItems: 1
                    },
                    groupIdToken: {
                        $ref: "#/definitions/IdTokenType"
                    },
                    language2: {
                        description: "Second preferred user interface language of identifier user. Dont use when language1 is omitted, has to be different from language1. Contains a language code as defined in <<ref-RFC5646,[RFC5646]>>.\r\n",
                        type: "string",
                        maxLength: 8
                    },
                    personalMessage: {
                        $ref: "#/definitions/MessageContentType"
                    }
                },
                required: ["status"]
            },
            IdTokenType: {
                description: "Contains a case insensitive identifier to use for the authorization and the type of authorization to support multiple forms of identifiers.",
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    additionalInfo: {
                        type: "array",
                        items: {
                            $ref: "#/definitions/AdditionalInfoType"
                        },
                        minItems: 1
                    },
                    idToken: {
                        description: "IdToken is case insensitive. Might hold the hidden id of an RFID tag, but can for example also contain a UUID.",
                        type: "string",
                        maxLength: 36
                    },
                    type: {
                        $ref: "#/definitions/IdTokenEnumType"
                    }
                },
                required: ["idToken", "type"]
            },
            MessageContentType: {
                description: "Contains message details, for a message to be displayed on a Charging Station.",
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    format: {
                        $ref: "#/definitions/MessageFormatEnumType"
                    },
                    language: {
                        description: "Message language identifier. Contains a language code as defined in <<ref-RFC5646,[RFC5646]>>.",
                        type: "string",
                        maxLength: 8
                    },
                    content: {
                        description: "Message contents.",
                        type: "string",
                        maxLength: 512
                    }
                },
                required: ["format", "content"]
            }
        },
        type: "object",
        properties: {
            customData: {
                $ref: "#/definitions/CustomDataType"
            },
            localAuthorizationList: {
                type: "array",
                items: {
                    $ref: "#/definitions/AuthorizationData"
                },
                minItems: 1
            },
            versionNumber: {
                description: "In case of a full update this is the version number of the full list. In case of a differential update it is the version number of the list after the update has been applied.",
                type: "integer"
            },
            updateType: {
                $ref: "#/definitions/UpdateEnumType"
            }
        }
    }

    validate = (formData, errors) => {
        let json = null;
        if (formData.hasOwnProperty("customData")) {
            if (formData.customData.hasOwnProperty("jsonString")) {
                if (formData.customData.jsonString.length > 0) {
                    try {
                        json = JSON.parse(formData.customData.jsonString);
                        if(!json.hasOwnProperty("vendorId")){
                            errors.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                        }
                    } catch {
                        errors.customData.jsonString.addError("cannot parse this field into JSON string");
                    }
                }
            }
        }
        formData.localAuthorizationList.forEach((val_i, i) => {
            if (val_i.customData.jsonString.length > 0) {
                try {
                    json = JSON.parse(val_i.customData.jsonString);
                    if(!json.hasOwnProperty("vendorId")){
                        errors.localAuthorizationList[i].customData.jsonString.addError("JSON string should have \"vendorId\" property");
                    }
                } catch {
                    errors.localAuthorizationList[i].customData.jsonString.addError("cannot parse this field into JSON string");
                }
            }
            if (val_i.idToken.customData.jsonString.length > 0) {
                try {
                    json = JSON.parse(val_i.idToken.customData.jsonString);
                    if(!json.hasOwnProperty("vendorId")){
                        errors.localAuthorizationList[i].idToken.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                    }
                } catch {
                    errors.localAuthorizationList[i].idToken.customData.jsonString.addError("cannot parse this field into JSON string");
                }
            }
            val_i.idToken.additionalInfo.forEach((val_j, j) => {
                if (val_j.customData.jsonString.length > 0) {
                    try {
                        json = JSON.parse(val_j.customData.jsonString);
                        if(!json.hasOwnProperty("vendorId")){
                            errors.localAuthorizationList[i].idToken.additionalInfo[j].customData.jsonString.addError("JSON string should have \"vendorId\" property");
                        }
                    } catch {
                        errors.localAuthorizationList[i].idToken.additionalInfo[j].customData.jsonString.addError("cannot parse this field into JSON string");
                    }
                }
            });
            if (val_i.idTokenInfo.customData.jsonString.length > 0) {
                try {
                    json = JSON.parse(val_i.idTokenInfo.customData.jsonString);
                    if(!json.hasOwnProperty("vendorId")){
                        errors.localAuthorizationList[i].idTokenInfo.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                    }
                } catch {
                    errors.localAuthorizationList[i].idTokenInfo.customData.jsonString.addError("cannot parse this field into JSON string");
                }
            }
            if (val_i.idTokenInfo.groupIdToken.customData.jsonString.length > 0) {
                try {
                    json = JSON.parse(val_i.idTokenInfo.groupIdToken.customData.jsonString);
                    if(!json.hasOwnProperty("vendorId")){
                        errors.localAuthorizationList[i].idTokenInfo.groupIdToken.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                    }
                } catch {
                    errors.localAuthorizationList[i].idTokenInfo.groupIdToken.customData.jsonString.addError("cannot parse this field into JSON string");
                }
            }
            val_i.idTokenInfo.groupIdToken.additionalInfo.forEach((val_j, j) => {
                if (val_j.customData.jsonString.length > 0) {
                    try {
                        json = JSON.parse(val_j.customData.jsonString);
                        if(!json.hasOwnProperty("vendorId")){
                            errors.localAuthorizationList[i].idTokenInfo.groupIdToken.additionalInfo[j].customData.jsonString.addError("JSON string should have \"vendorId\" property");
                        }
                    } catch {
                        errors.localAuthorizationList[i].idTokenInfo.groupIdToken.additionalInfo[j].customData.jsonString.addError("cannot parse this field into JSON string");
                    }
                }
            });
            if (val_i.idTokenInfo.personalMessage.customData.jsonString.length > 0) {
                try {
                    json = JSON.parse(val_i.idTokenInfo.personalMessage.customData.jsonString);
                    if(!json.hasOwnProperty("vendorId")){
                        errors.localAuthorizationList[i].idTokenInfo.personalMessage.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                    }
                } catch {
                    errors.localAuthorizationList[i].idTokenInfo.personalMessage.customData.jsonString.addError("cannot parse this field into JSON string");
                }
            }
        });
        return errors;
    }

    onSubmit = async ({formData}) => {
        formData = this.formatCustomData(formData);
        formData.localAuthorizationList.forEach((value_i, index_i) => {
            formData.localAuthorizationList[index_i] = this.formatCustomData(value_i);
            formData.localAuthorizationList[index_i].idToken = this.formatCustomData(value_i.idToken);
            formData.localAuthorizationList[index_i].idToken.additionalInfo.forEach((value_j, index_j) => {
                formData.localAuthorizationList[index_i].idToken.additionalInfo[index_j] = this.formatCustomData(value_j);
            });
            formData.localAuthorizationList[index_i].idTokenInfo = this.formatCustomData(value_i.idTokenInfo);
            formData.localAuthorizationList[index_i].idTokenInfo.groupIdToken = this.formatCustomData(value_i.idTokenInfo.groupIdToken);
            formData.localAuthorizationList[index_i].idTokenInfo.groupIdToken.additionalInfo.forEach((value_j, index_j) => {
                formData.localAuthorizationList[index_i].idTokenInfo.groupIdToken.additionalInfo[index_j] = this.formatCustomData(value_j);
            });
            formData.localAuthorizationList[index_i].idTokenInfo.personalMessage = this.formatCustomData(value_i.idTokenInfo.personalMessage);
        });
        const json = this.formatData(formData);
        window.alert(json);
        await axios.post(`http://${IP_AND_PORT}/api/post-request`,
             json, {headers: {'Content-Type': 'application/json'}});
    }
}

export default SendLocalList;