import 'bootstrap/dist/css/bootstrap.min.css'
import React, {useEffect, useState} from "react";
import {Col, Container, Navbar, Row} from "react-bootstrap";
import { LazyLog } from 'react-lazylog';
import {IP_AND_PORT} from "./config";
import { useCookies } from "react-cookie";
import { v4 as uuidv4 } from 'uuid';
import MainForm from "./components/MainForm"

function App() {
    const [cookies, setCookie] = useCookies(["ocppWebClientId"])
    const size = useWindowSize();
    let socket = null;
    let uid;

    function handleUidChange() {
        if(!cookies.ocppWebClientId){
            setCookie('ocppWebClientId', uuidv4(), { path: '/' })
        }
        return cookies.ocppWebClientId;
    }

    uid = handleUidChange();

    return (
        <div className="App bg-dark" style={{height: size.height}}>
            <Container fluid>
                <Navbar bg="dark" expand="lg" className='navbar-dark'>
                    <Navbar.Brand href="#" style={{color: "orange"}}>Ocpp Server web client (protocol version: 2.0.1)</Navbar.Brand>
                </Navbar>
            </Container>
            <Container fluid>
                <Row>
                    <Col lg='5' style={{overflowY: "auto", height: size.height - 56}}>
                        <MainForm uid={uid}/>
                    </Col>
                    <Col>
                        <LazyLog height={size.height - 56}
                                 enableSearch url={`ws://${IP_AND_PORT}/api/webclient-socket/` + uid}
                                 websocket={true} websocketOptions={{
                            onOpen: (e, sock) => {
                                socket = sock;
                                cookies.ocppWebClientId && sock.send(JSON.stringify({message: "connect"}));
                            },
                            onClose: () => {
                                cookies.ocppWebClientId && socket && socket.send(JSON.stringify({message: "disconnect"}));
                            },
                            formatMessage: e => JSON.parse(e).message,
                        }} />
                    </Col>
                </Row>
            </Container>
        </div>
    );
}

// Hook
function useWindowSize() {
    // Initialize state with undefined width/height so server and client renders match
    // Learn more here: https://joshwcomeau.com/react/the-perils-of-rehydration/
    const [windowSize, setWindowSize] = useState({
        width: 1024,
        height: 768,
    });

    useEffect(() => {
        // Handler to call on window resize
        function handleResize() {
            // Set window width/height to state
            setWindowSize({width: window.innerWidth, height: window.innerHeight});
        }

        // Add event listener
        window.addEventListener("resize", handleResize);

        // Call handler right away so state gets updated with initial window size
        handleResize();

        // Remove event listener on cleanup
        return () => window.removeEventListener("resize", handleResize);
    }, []); // Empty array ensures that effect is only run on mount

    return windowSize;
}

export default App;