import PropTypes from "prop-types";
import Form from "@rjsf/bootstrap-4";
import React from "react";
import {Accordion, AccordionCollapse, Button, ButtonGroup, ButtonToolbar, Card, ListGroup} from "react-bootstrap";
import axios from "axios";
import {IP_AND_PORT} from "../../config";

class _common extends React.Component {
    schema = {};
    requestName = "";

    formatData = (formData) => {
        let newFormData = {clientId: this.props.uid, charger: this.props.charger, selected: this.requestName,
            payload: formData};
        return JSON.stringify(newFormData);
    };

    ObjectFieldTemplate = (props) => {
        return (
            <Card border="dark">
                <Card.Header className="text-white bg-dark">
                    {props.title}
                </Card.Header>
                <Card.Body>
                    <Card.Text className="text-muted small">{props.description}</Card.Text>
                    {props.properties.map((value, index) => <div className="property-wrapper"
                                                                 key={index}>{value.content}</div>)}
                </Card.Body>
            </Card>
        );
    }

    ArrayFieldTemplate = (props) => {
        return (
            <Card border="dark">
                <Card.Header className="text-white bg-dark">{props.title}</Card.Header>
                <ListGroup variant="flush">
                    {props.items.map((value, index) =>
                        <ListGroup.Item>
                            <ButtonToolbar className="justify-content-between">
                                {value.children}
                                <ButtonGroup size="sm">
                                    {value.hasRemove ?
                                        <Button className="bg-dark text-white" type="button"
                                                onClick={value.onDropIndexClick(index)}><b>-</b></Button> :
                                        <Button className="bg-dark text-white" type="button" disabled><b>-</b></Button>
                                    }
                                    {value.hasMoveUp ?
                                        <Button className="bg-dark text-white" type="button"
                                                onClick={value.onReorderClick(index, index - 1)}><b>&uarr;</b></Button> :
                                        <Button className="bg-dark text-white" type="button"
                                                disabled><b>&uarr;</b></Button>
                                    }
                                    {value.hasMoveDown ?
                                        <Button className="bg-dark text-white" type="button"
                                                onClick={value.onReorderClick(index, index + 1)}><b>&darr;</b></Button> :
                                        <Button className="bg-dark text-white" type="button"
                                                disabled><b>&darr;</b></Button>
                                    }
                                </ButtonGroup>
                            </ButtonToolbar>
                        </ListGroup.Item>
                    )
                    }
                    {props.canAdd ?
                        <Button type="button"
                                className="list-group-item list-group-item-action bg-dark text-white text-center"
                                onClick={props.onAddClick}>+</Button> :
                        <Button type="button"
                                className="list-group-item list-group-item-action  bg-dark text-white text-center"
                                disabled>+</Button>}
                </ListGroup>
            </Card>
        );
    }

    formatCustomData = (parent) => {
        let newParent = parent;
        if (newParent.hasOwnProperty("customData")) {
            if (newParent.customData.hasOwnProperty("jsonString")) {
                const json = JSON.parse(newParent.customData.jsonString);
                if (json.hasOwnProperty("vendorId")) {
                    if (json.vendorId !== "") {
                        newParent.customData = json;
                    } else {
                        delete newParent["customData"]
                    }
                }
            }
        }
        return newParent;
    }


    validate = (formData, errors) => {
        if (formData.hasOwnProperty("customData")) {
            if (formData.customData.hasOwnProperty("jsonString")) {
                if (formData.customData.jsonString.length > 0) {
                    try {
                        const json = JSON.parse(formData.customData.jsonString);
                        if(!json.hasOwnProperty("vendorId")){
                            errors.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                        }
                    } catch (e) {
                        errors.customData.jsonString.addError("cannot parse this field into JSON string");
                    }
                }
            }
        }
        return errors;
    }

    onSubmit = async ({formData}) => {
        formData = this.formatCustomData(formData);
        const json = this.formatData(formData);
        window.alert(json);
        await axios.post(`http://${IP_AND_PORT}/api/post-request`,
            json, {headers: {'Content-Type': 'application/json'}});
    }

    render() {
        return (
            <Card  style={{backgroundColor: "#d3d3d3"}}>
                <Card.Header>
                    <Accordion.Toggle as={Card.Header} eventKey={this.props.eventKey}>
                        {this.props.title}
                    </Accordion.Toggle>
                </Card.Header>
                <AccordionCollapse eventKey={this.props.eventKey}>
                    <Card.Body>
                        <Form schema={this.schema} onSubmit={this.onSubmit} validate={this.validate}
                              ObjectFieldTemplate={this.ObjectFieldTemplate}
                              ArrayFieldTemplate={this.ArrayFieldTemplate}

                        />
                    </Card.Body>
                </AccordionCollapse>
            </Card>
        );
    }
}

_common.propTypes = {
    charger: PropTypes.string.isRequired,
    eventKey: PropTypes.string.isRequired,
    uid: PropTypes.string.isRequired
};

export default _common;