import _common from "./_common";
import axios from "axios";
import {IP_AND_PORT} from "../../config";

class DeleteCertificate extends _common {
    requestName = "DeleteCertificate";
    schema = {
        definitions: {
            CustomDataType: {
                description: "This class can be extended with arbitrary JSON properties to allow adding custom data.",
                type: "object",
                properties: {
                    jsonString: {
                        title: "JSON String",
                        description: "If this field is not changed, the whole customData property will be removed",
                        type: "string",
                        default: "{\"vendorId\": \"\"}",
                        maxLength: 10000
                    }
                }
            },
            HashAlgorithmEnumType: {
                description: "Used algorithms for the hashes provided.",
                type: "string",
                enum: ["SHA256", "SHA384", "SHA512"]
            },
            CertificateHashDataType: {
                type: "object",
                properties: {
                    customData: {
                        $ref: "#/definitions/CustomDataType"
                    },
                    hashAlgorithm: {
                        $ref: "#/definitions/HashAlgorithmEnumType"
                    },
                    issuerNameHash: {
                        description: "Hashed value of the Issuer DN (Distinguished Name).",
                        type: "string",
                        maxLength: 128
                    },
                    issuerKeyHash: {
                        description: "Hashed value of the issuers public key\r\n",
                        type: "string",
                        maxLength: 128
                    },
                    serialNumber: {
                        description: "The serial number of the certificate.\r\n",
                        type: "string",
                        maxLength: 40
                    }
                },
                required: ["hashAlgorithm", "issuerNameHash", "issuerKeyHash", "serialNumber"]
            }
        },
        type: "object",
        properties: {
            customData: {
                $ref: "#/definitions/CustomDataType"
            },
            certificateHashData: {
                $ref: "#/definitions/CertificateHashDataType"
            }
        },
        required: ["certificateHashData"]
    }

    validate = (formData, errors) => {
        let json = null;
        if (formData.customData.jsonString.length > 0) {
            try {
                json = JSON.parse(formData.customData.jsonString);
                if (!json.hasOwnProperty("vendorId")) {
                    errors.customData.jsonString.addError("JSON string should have \"vendorId\" property");
                }
            } catch {
                errors.customData.jsonString.addError("cannot parse this field into JSON string");
            }
        }
        if (formData.certificateHashData.customData.jsonString.length > 0) {
            try {
                json = JSON.parse(formData.certificateHashData.customData.jsonString);
                if (!json.hasOwnProperty("vendorId")) {
                    errors.certificateHashData.customData.jsonString.addError("JSON string should have \"vendorId\" property")
                }
            } catch {
                errors.certificateHashData.customData.jsonString.addError("cannot parse this field into JSON string");
            }
        }
        return errors;
    }

    onSubmit = async ({formData}) => {
        formData = this.formatCustomData(formData);
        formData.certificateHashData = this.formatCustomData(formData.certificateHashData);
        const json = this.formatData(formData);
        window.alert(json);
        await axios.post(`http://${IP_AND_PORT}/api/post-request`,
            json, {headers: {'Content-Type': 'application/json'}});
    }
}

export default DeleteCertificate;